import {
	LOGIN,
	LOGOUT,
	SET_AUTH_LOADING,
	SET_MAIL_SENDED,
	SET_ERRORS,
	RESET_ERRORS,
	AuthActionTypes,
	AuthState,
} from "../types/auth-types";

const initialState: AuthState = {
	isLoggedIn: localStorage && localStorage.getItem("rsIsLogged") === "true",
	loading: false,
	mailIsSended: false,
	errors: {},
};

export function authReducer(state = initialState, action: AuthActionTypes): AuthState {
	switch (action.type) {
		case LOGIN:
			localStorage && localStorage.setItem("rsIsLogged", "true");
			return {
				...state,
				isLoggedIn: true,
			};
		case LOGOUT:
			localStorage && localStorage.removeItem("rsIsLogged");
			return {
				...state,
				isLoggedIn: false,
			};
		case SET_AUTH_LOADING:
			return {
				...state,
				loading: action.payload,
			};
		case SET_MAIL_SENDED:
			return {
				...state,
				mailIsSended: action.payload,
			};
		case SET_ERRORS:
			return {
				...state,
				errors: action.payload,
			};
		case RESET_ERRORS:
			const errorsName = action.payload;
			return {
				...state,
				errors: errorsName ? {...state.errors, [errorsName]: ""} : {},
			};
		default:
			return state;
	}
}
