import React, {useRef, useState, useEffect} from "react";

import styles from "./le.module.sass";

type ILocationInput = {
	name: string;
	value?: string;
	placeholder?: string;
	onChange: (lat: number, lng: number, address: string) => void;
};

export const LocationInput: React.FC<ILocationInput> = ({
	name,
	value,
	onChange,
	placeholder = "",
}) => {
	const inputRef = useRef<HTMLInputElement>(null);
	const [inputValue, setInputValue] = useState<string>(value || "");

	useEffect(() => {
		if (inputRef.current === null) return;
		const autocomplete = new google.maps.places.Autocomplete(inputRef.current);
		window.google.maps.event.addListener(autocomplete, "place_changed", () => {
			const place = autocomplete.getPlace();
			if (!place.geometry) return;
			setInputValue(place.formatted_address || "");
			onChange(
				place.geometry.location.lat(),
				place.geometry.location.lng(),
				place.formatted_address || "",
			);
		});
		//eslint-disable-next-line
	}, []);

	const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
		setInputValue(e.target.value);
	};

	const handleGetLocation = (evt: React.MouseEvent) => {
		evt.preventDefault();
		if (window.google) {
			const geocoder = new google.maps.Geocoder();
			if ("geolocation" in navigator) {
				navigator.geolocation.getCurrentPosition((event) => {
					geocoder.geocode(
						{
							location: {
								lat: event.coords.latitude,
								lng: event.coords.longitude,
							},
						},
						function (results, status) {
							if (status === "OK") {
								const place = results[0];
								if (place && place.geometry) {
									setInputValue(place.formatted_address || "");
									onChange(
										place.geometry.location.lat(),
										place.geometry.location.lng(),
										place.formatted_address || "",
									);
									// if (inputRef.current) inputRef.current.dispatchEvent(new Event("blur"));
								} else {
									console.error("No results found");
								}
							} else {
								console.error("Geocoder failed due to: " + status);
							}
						},
					);
				});
			}
		}
	};

	return (
		<div className={`${styles["control-wrap"]} ${styles["control-wrap--input"]}`}>
			<i className="fico fico-place is-active" onClick={handleGetLocation} />
			<input
				className={styles.input}
				ref={inputRef}
				type="text"
				name={name}
				value={inputValue}
				placeholder={placeholder}
				onChange={handleChange}
			/>
		</div>
	);
};
