import React, {forwardRef} from "react";
import styles from "./de.module.sass";

interface IDateInput {
	value: string;
	onClick: () => void;
}

export const DateInput = forwardRef<HTMLDivElement, IDateInput>(({value, onClick}, ref) => {
	const [date, time] = value ? value.split(" || ") : ["--- | -- | ----", "--:--"];

	return (
		<div className={styles["control-wrap"]} ref={ref} onClick={onClick}>
			<i className="fico fico-calendar" />
			<div className={styles["date-val"]}>
				<div className={styles.date}>{date}</div>
				<div className={styles.time}>{time}</div>
			</div>
		</div>
	);
});
