import React, {useState} from "react";
import classnames from "classnames";
import InputMask from "react-input-mask";

import styles from "./extras.module.sass";

interface extraProps extends IExtra {
	currency: string | null;
	editable: boolean;
	onChange: (id: number, quantity: number) => void;
}

export const Extra: React.FC<extraProps> = ({
	id,
	title,
	icon,
	price_day,
	max_quantity,
	quantity = 0,
	type,
	price,
	editable,
	currency,
	onChange,
}) => {
	const [check, setCheck] = useState(quantity > 0);
	const [count, setCount] = useState(quantity);

	const onCheck = (e: React.ChangeEvent<HTMLInputElement>): void => {
		const val = !check;
		if (val) {
			setCount(1);
			onChange(id, 1);
		} else {
			setCount(0);
			onChange(id, 0);
		}
		setCheck(val);
	};

	const changeQuantity = (q: number) => {
		let newQ = q;
		if (q <= 0) {
			newQ = 0;
			setCheck(false);
		} else if (max_quantity && q > max_quantity) {
			newQ = max_quantity;
			setCheck(true);
		} else {
			newQ = q;
			setCheck(true);
		}

		if (count !== newQ) {
			setCount(newQ);
			onChange(id, newQ);
		}
	};

	const onChangeQuantityInput = (e: React.ChangeEvent<HTMLInputElement>): void => {
		changeQuantity(+e.target.value);
	};

	const decrement = (e: React.MouseEvent<HTMLButtonElement>): void => {
		changeQuantity(count - 1);
	};

	const increment = (e: React.MouseEvent<HTMLButtonElement>): void => {
		changeQuantity(count + 1);
	};

	return (
		<div className={styles.item}>
			<div className={styles.data}>
				{editable && (
					<input
						className={styles["hidden-input"]}
						type="checkbox"
						name={`extra_${id}`}
						value={count}
						id={`extra_${id}`}
						checked={check}
						onChange={onCheck}
					/>
				)}
				<label
					className={classnames(styles.label, {
						[styles["label--check"]]: editable,
					})}
					htmlFor={editable ? `extra_${id}` : undefined}>
					{`${!editable ? `${count} x ` : ""}${title}`}
				</label>
				{editable && (
					<div className={styles.quantity}>
						<button
							className={`${styles.quantity__button} ${styles.left}`}
							type="button"
							onClick={decrement}
						/>
						<InputMask
							className={styles.quantity__input}
							type="text"
							mask="999"
							maskChar={null}
							value={count}
							onChange={onChangeQuantityInput}
						/>
						<button
							className={`${styles.quantity__button} ${styles.right}`}
							type="button"
							onClick={increment}
						/>
					</div>
				)}
			</div>
			<div className="price">
				{price}&nbsp;{currency}
			</div>
		</div>
	);
};
