import {batch} from "react-redux";
import ReactTooltip from "react-tooltip";
import queryString, {ParsedQuery} from "query-string";
import {
	SET_RESERVATION_LOADING,
	SET_RESERVATIONS,
	UPDATE_FILTERS,
	SET_RESERVATION,
	UPDATE_RESERVATION,
	ReservationsActionTypes,
	ReservationsThunkAction,
} from "../types/reservations-types";
import rsApi from "../../api/rsApi";

const setReservationLoading = (loading: boolean): ReservationsActionTypes => {
	return {
		type: SET_RESERVATION_LOADING,
		payload: loading,
	};
};

const setReservations = (newReservationsData: IReservationsData): ReservationsActionTypes => {
	return {
		type: SET_RESERVATIONS,
		payload: newReservationsData,
	};
};

const updateFilters = (queryParams: ParsedQuery): ReservationsActionTypes => {
	return {
		type: UPDATE_FILTERS,
		payload: queryParams,
	};
};

const setReservation = (reservationData: IReservation): ReservationsActionTypes => {
	return {
		type: SET_RESERVATION,
		payload: reservationData,
	};
};

const setReservationPart = (data: Partial<IReservation>): ReservationsActionTypes => {
	return {
		type: UPDATE_RESERVATION,
		payload: data,
	};
};

/**
 * Get list of reservations
 */
export const getReservations = (query: string): ReservationsThunkAction => async (dispatch) => {
	batch(() => {
		dispatch(setReservationLoading(true));
		dispatch(updateFilters(queryString.parse(query)));
	});
	try {
		const response = await rsApi.getReservations(query);
		if (response.data.status) {
			const {items, pagination} = response.data.data;
			batch(() => {
				dispatch(
					setReservations({
						reservations: items,
						pagination,
					}),
				);
				dispatch(setReservationLoading(false));
			});
			ReactTooltip.rebuild();
		}
	} catch (error) {
		console.error(error);
		dispatch(setReservationLoading(false));
	}
};

/**
 * Get one reservation by id
 */
export const getReservation = (id: string | number): ReservationsThunkAction => async (
	dispatch,
	getState,
) => {
	const {data, loading} = getState().reservations;
	if (loading) return;

	const reservationPart = data.reservations.filter((r) => r.id === id)[0];
	batch(() => {
		dispatch(setReservationLoading(true));
		reservationPart && dispatch(setReservation(reservationPart));
	});

	try {
		const [reserveRes, extrasRes] = await Promise.all([
			rsApi.getReservation(id),
			rsApi.getReservationExtras(id),
		]);
		if (reserveRes.data.status && extrasRes.data.status) {
			const {extras, insurances, taxes, delivery_data} = extrasRes.data.data;
			batch(() => {
				dispatch(
					setReservation({
						...reserveRes.data.data,
						extras_data: {
							extras,
							insurances,
							taxes,
							delivery_data,
						},
					}),
				);
				dispatch(setReservationLoading(false));
			});
			ReactTooltip.rebuild();
		}
	} catch (error) {
		console.error(error);
		dispatch(setReservationLoading(false));
	}
};

/**
 * Update current reservation
 */
export const updateReservation = (
	data: any,
	onSuccess?: () => void,
	onError?: (error?: any) => void,
): ReservationsThunkAction => async (dispatch, getState) => {
	const {reservation, loading} = getState().reservations;
	if (loading || !reservation) return;

	dispatch(setReservationLoading(true));

	try {
		const response = await rsApi.putReservation(reservation.id, data);
		if (response.data.status) {
			batch(() => {
				dispatch(setReservationPart(response.data.data));
				dispatch(setReservationLoading(false));
			});
			ReactTooltip.rebuild();
			onSuccess && onSuccess();
		}
	} catch (error) {
		console.error(error);
		dispatch(setReservationLoading(false));
		onError && onError(error);
	}
};

/**
 * Reject current reservation
 */
export const rejectReservation = (
	message: string = "",
	onSuccess?: () => void,
	onError?: (error?: any) => void,
): ReservationsThunkAction => async (dispatch, getState) => {
	const {reservation, loading} = getState().reservations;
	if (loading || !reservation) return;

	dispatch(setReservationLoading(true));

	try {
		const response = await rsApi.putReservationReject(reservation.id, {message});
		if (response.data.status) {
			batch(() => {
				dispatch(
					setReservationPart({
						status: response.data.data.status,
						annul: response.data.data.message,
					}),
				);
				dispatch(setReservationLoading(false));
			});
			ReactTooltip.rebuild();
			onSuccess && onSuccess();
		}
	} catch (error) {
		console.error(error);
		dispatch(setReservationLoading(false));
		onError && onError(error);
	}
};

/**
 * Update extras
 */
export const updateExtras = (
	insurance: IInsurance[],
	extras: IExtra[],
): ReservationsThunkAction => async (dispatch, getState) => {
	const {reservation} = getState().reservations;
	if (!reservation) return;

	const extrasData = {
		insurance: insurance.filter((i) => i.checked === true)[0].id,
		extras: extras.reduce((prev: {[key: string]: number}[], cur) => {
			if (cur.quantity > 0) prev.push({quantity: cur.quantity, id: cur.id});
			return prev;
		}, []),
	};

	try {
		const response = await rsApi.putReservationExtras(reservation.id, extrasData);
		if (response.data.status) {
			const {extras, insurances, taxes, prices, delivery_data} = response.data.data;
			dispatch(
				setReservationPart({
					extras_data: {
						extras,
						insurances,
						taxes,
						delivery_data,
					},
					prices,
				}),
			);
			ReactTooltip.rebuild();
		}
	} catch (error) {
		console.error(error);
	}
};

/**
 * Upload signature for reservation
 */
export const uploadSignature = (
	signature: string = "",
	file: Blob,
	onSuccess?: () => void,
	onError?: (error?: any) => void,
): ReservationsThunkAction => async (dispatch, getState) => {
	const {reservation, loading} = getState().reservations;
	if (loading || !reservation) return;

	dispatch(setReservationLoading(true));

	try {
		const signResponse = await rsApi.postSignature(reservation.id, file);
		if (signResponse.data.status) {
			const reservationResponse = await rsApi.getReservation(reservation.id);
			if (reservationResponse.data.status) {
				batch(() => {
					dispatch(setReservationPart(reservationResponse.data.data));
					dispatch(setReservationLoading(false));
				});
				ReactTooltip.rebuild();
				onSuccess && onSuccess();
			}
		}
	} catch (error) {
		console.error(error);
		dispatch(setReservationLoading(false));
		onError && onError(error);
	}
};
