import React from "react";
import {NavLink} from "react-router-dom";
import {useDispatch} from "react-redux";

import styles from "./nav.module.sass";

import {LogoutAction} from "../../redux/actions";

type NavItemType = {
	title: string;
	link: string;
};

const nav: NavItemType[] = [
	{
		title: "Reservation",
		link: "/reservations",
	},
	{
		title: "Payments",
		link: "/payments",
	},
	{
		title: "My account",
		link: "/account",
	},
];

export const Nav: React.FC = () => {
	const dispatch = useDispatch();

	const onExitClick = (e: React.MouseEvent<HTMLButtonElement>) => {
		dispatch(LogoutAction());
	};

	return (
		<ul className={styles.list}>
			{nav.map(({title, link}: NavItemType) => (
				<li key={title}>
					<NavLink to={link} className={styles.link} activeClassName={styles.active}>
						{title}
					</NavLink>
				</li>
			))}
			<li key="Exit">
				<button type="button" className={styles.link} onClick={onExitClick}>
					Exit
				</button>
			</li>
		</ul>
	);
};
