import React from "react";

import styles from "./todo.module.sass";

type Action = string;

interface ITodo {
	actions: {
		[key: string]: Action;
	};
	className?: string;
}

export const Todo: React.FC<ITodo> = ({actions, className}) => {
	const getActionsText = (count: number): string => {
		switch (count) {
			case 0:
			case undefined:
				return "";
			case 1:
				return "Necessary 1 action:";
			default:
				return `Necessary ${count} actions:`;
		}
	};

	const actionKeys = Object.keys(actions);

	return (
		<div className={`${styles.container}${className ? ` ${className}` : ""}`}>
			<div className={`${styles.label} todo__label`}>{getActionsText(actionKeys.length)}</div>
			<div className={`${styles.actions} todo__actions`}>
				{actionKeys.map((key, index) => (
					<span key={key} className={styles.action}>
						{actions[key]}
					</span>
				))}
			</div>
		</div>
	);
};
