import React from "react";
import classnames from "classnames";
import formatDistanceStrict from "date-fns/formatDistanceStrict";
import ReactTooltip from "react-tooltip";
import Skeleton from "react-loading-skeleton";
import {ucFirst, unspaceDate} from "../../helpers/utils";
import {Button, LinkButton, DateLocation, RentalTime, Todo} from "..";
import {VehicleParam} from "../VehicleParam/VehicleParam";

import vpStyles from "../VehicleParam/vp.module.sass";
import styles from "./rslist.module.sass";

import {useSearch} from "../../hooks";

interface IReservationListItem {
	reservation: IReservation;
	loading: boolean;
}
export const ReservationListItem: React.FC<IReservationListItem> = ({reservation, loading}) => {
	const {setLocationSearch} = useSearch();
	const {
		currency,
		date_from,
		date_to,
		id,
		insurance,
		location_from,
		location_to,
		mileage_limit,
		necessary_actions,
		prices,
		status,
		type,
		unique_number,
		vehicle,
	} = reservation;
	//get vehicle params
	const lowerCaseType = type.toLowerCase();
	const lowerCaseStatus = status.toLowerCase();

	const isRedjected = lowerCaseStatus === "reject" || lowerCaseStatus === "cancel";

	const {brand, mark, group, large_bags, number_seats, refill, transmission} = vehicle;

	const onStatusClick = (e: React.MouseEvent<HTMLSpanElement>): void => {
		setLocationSearch([
			{
				name: "reserved_status",
				value: lowerCaseStatus,
			},
		]);
	};

	const onTypeClick = (e: React.MouseEvent<HTMLSpanElement>): void => {
		setLocationSearch([
			{
				name: "type",
				value: lowerCaseType,
			},
		]);
	};

	return (
		<div
			className={classnames(styles.reservation, {
				"is-rejected": isRedjected,
				"is-keyless": lowerCaseType === "keyless",
				"is-standard": lowerCaseType === "standard",
			})}>
			<div className={styles.reservation__header}>
				<span className={styles.label}>
					Reservation: <span className={styles.number}>{unique_number}</span>
				</span>
				<div className={styles.status}>
					<span className={styles.label}>
						Rental type:{" "}
						<span className={styles["type-val"]} onClick={onTypeClick}>
							{ucFirst(type)}
						</span>
					</span>
					<span className={styles.label}>
						Status:{" "}
						<span className={styles["status-val"]} onClick={onStatusClick}>
							{ucFirst(status)}
						</span>
					</span>
				</div>
			</div>
			<div className={styles.reservation__body}>
				<div className={styles.side}>
					<div className={styles.vehicle}>
						<div className={styles.vehicle__info}>
							<div className={styles.vehicle__img}>
								<img src={vehicle.thumbnail} alt="" />
							</div>
							<div className={styles.vehicle__group}>{group}</div>
							<div className={styles.vehicle__title}>
								{brand} {mark}
							</div>
							<div className={styles.vehicle__similar}>or similar</div>
						</div>
						<div className={styles.vehicle__info}>
							{(number_seats || large_bags) && (
								<div className={vpStyles.vp__group}>
									{number_seats && <VehicleParam icon="driver" value={number_seats} />}
									{large_bags && <VehicleParam icon="profile" value={large_bags} />}
								</div>
							)}
							{transmission.name && (
								<VehicleParam icon="transmission" value={transmission.name} isTransmission />
							)}
							{mileage_limit && <VehicleParam icon="gauge" value={mileage_limit} />}
							{refill && <VehicleParam icon="gas-station2" value={refill} />}
						</div>
					</div>
				</div>
				<div className={styles.side}>
					<div className={styles.details}>
						<div className={`${styles.details__column} ${styles["details__column--mob"]}`}>
							<div className={styles["mob-flex"]}>
								<div className={styles.label}>Pick Up</div>
								<DateLocation
									date={date_from}
									location={
										location_from.type === "custom"
											? location_from.address
											: location_from.office.title
									}
								/>
							</div>
							<div className={styles["mob-flex"]}>
								<div className={styles.label}>Return</div>
								<DateLocation
									date={date_to}
									location={
										location_to.type === "custom" ? location_to.address : location_to.office.title
									}
								/>
							</div>
						</div>
						<div className={styles.details__column}>
							<div className={styles.label}>
								Rental period{" "}
								{formatDistanceStrict(
									new Date(unspaceDate(date_to)),
									new Date(unspaceDate(date_from)),
									{
										unit: "day",
										roundingMethod: "ceil",
									},
								)}
							</div>
							<div className={styles.label}>Extras</div>
							<p className={styles.extras}>
								{insurance.name}&nbsp;INSURNCE with {insurance.damage_excess}&nbsp;{currency.symbol}{" "}
								Excess
							</p>
							<div className={styles.details__group}>
								<div className={styles["details__group-item"]}>
									<div className={styles.label}>Subtotal:</div>
									<div className={styles.price}>
										{prices.total}&nbsp;{currency.symbol}
									</div>
								</div>
								<div className={styles["details__group-item"]}>
									<div className={styles.label}>Paid:</div>
									<div className={styles.price}>
										{prices.paid}&nbsp;{currency.symbol}
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				{loading && (
					<Skeleton width={"100%"} height={"100%"} className={styles.reservation__loading} />
				)}
			</div>
			<div className={styles.reservation__footer}>
				{!isRedjected && lowerCaseStatus !== "done" && (
					<div className={styles["reservation__bottom-info"]}>
						<RentalTime date={date_from} className={styles["rental-time"]} />
						<Todo className={styles.reservation__todo} actions={necessary_actions} />
					</div>
				)}
				<div className={styles.reservation__btns}>
					<LinkButton text="Detail" to={`/reservations/${id}`} disabled={loading} />
					{!isRedjected && lowerCaseStatus !== "done" && lowerCaseType === "keyless" && (
						<Button text="Start rental" color="green" disabled={loading} onClick={() => {}} />
					)}
				</div>
			</div>
			<ReactTooltip />
		</div>
	);
};
