import React, {useRef, useEffect} from "react";
import {Redirect, useLocation} from "react-router-dom";
import queryString from "query-string";
import {useDispatch} from "react-redux";
import {Loader} from "../components";
import {loginAction} from "../redux/actions";

const parseToken = (tokenData: string | string[] | null) => {
	let parsedToken: string | undefined | null = "";
	if (Array.isArray(tokenData)) {
		parsedToken = tokenData.find((t) => !!t);
	} else {
		parsedToken = tokenData;
	}
	return parsedToken;
};

export const Auth: React.FC = () => {
	const {search} = useLocation();
	const {token} = queryString.parse(search);
	const dispatch = useDispatch();

	const tokenIsValid = useRef(parseToken(token));

	useEffect(() => {
		if (tokenIsValid.current) dispatch(loginAction(tokenIsValid.current));
	}, [dispatch]);

	return tokenIsValid.current ? <Loader /> : <Redirect to="/login" />;
};
