import React from "react";
import {shallowEqual} from "react-redux";
import {useTypedSelector} from "./redux";
import {BrowserRouter} from "react-router-dom";
import {AuthRoutes, PersonalRoutes} from "./routes";

const App: React.FC = () => {
	const {isLoggedIn} = useTypedSelector((store) => store.auth, shallowEqual);
	// const dispatch = useDispatch();

	return <BrowserRouter>{isLoggedIn ? <PersonalRoutes /> : <AuthRoutes />}</BrowserRouter>;
};

export default App;
