import axios from "axios";
import {API_ROOT} from "../helpers/constants";
import store from "../redux/store";
import {LOGOUT} from "../redux/types/auth-types";

type tokenType = string;

interface ITokensBackData {
	access_token?: tokenType;
	expires_in?: number;
	refresh_token?: tokenType;
}

interface ITokensData {
	accessToken: tokenType;
	exp: number;
	refreshToken: tokenType;
}

type resolveType = (value?: unknown) => void;

const Auth = (function () {
	//#region Private methods

	let BEARER: string = "";
	let refreshToken: string = localStorage.getItem("refreshToken") || "";
	let accessTokenExp: number = 0;

	function _setAuthData({accessToken, exp, refreshToken}: ITokensData) {
		Auth.setRefreshToken(refreshToken);
		Auth.setBearer(accessToken);
		Auth.setAccessTokenExp(exp);
	}

	function _parseTokenData(tokensData: ITokensBackData) {
		return {
			accessToken: tokensData.access_token,
			exp: new Date().getTime() + (tokensData.expires_in || 0),
			refreshToken: tokensData.refresh_token,
		} as ITokensData;
	}

	function _resetAuthData(): void {
		// reset tokens
		Auth.setRefreshToken("");
		Auth.setBearer("");
		Auth.setAccessTokenExp(0);
	}

	function _debounce<T extends Function>(inner: T, ms: number = 0) {
		let timer: ReturnType<typeof setTimeout> | null = null;
		let resolves: resolveType[] = [];

		return function () {
			if (timer !== null) {
				clearTimeout(timer);
				timer = null;
			}

			timer = setTimeout(() => {
				const result = inner();
				resolves.forEach((r) => r(result));
				resolves = [];
			}, ms);

			return new Promise((resolve) => resolves.push(resolve));
		};
	}
	//#endregion

	return {
		//#region Public methods
		makeLogin: async function (token: ILoginData) {
			const response = await axios.post(
				`${API_ROOT}/auth`,
				{"one-time-token": token},
				// {withCredentials: true}
			);
			_setAuthData(_parseTokenData(response.data.data));
			return response;
		},

		refreshTokens: async function () {
			try {
				const response = await axios.post(
					`${API_ROOT}/auth/refresh`,
					{"refresh-token": refreshToken},
					// {withCredentials: true}
				);
				_setAuthData(_parseTokenData(response.data.data));
				return response;
			} catch (error) {
				_resetAuthData();
				localStorage.removeItem("rsIsLogged");
				store.dispatch({type: LOGOUT});
				throw new Error(error);
			}
		},

		debounceRefreshTokens: _debounce(() => {
			return Auth.refreshTokens();
		}, 100),

		setBearer: function (accessToken: tokenType) {
			BEARER = `Bearer ${accessToken}`;
		},

		getBearer: function (): string {
			return BEARER;
		},

		setAccessTokenExp: function (exp: number): void {
			accessTokenExp = exp;
		},

		getAccessTokenExp: function (): number {
			return accessTokenExp;
		},

		isAccessTokenExpired: function (): boolean {
			const accessTokenExpDate = accessTokenExp - 5000;
			const nowTime = new Date().getTime();
			return accessTokenExpDate <= nowTime;
		},

		hasRefreshToken: function (): boolean {
			return Boolean(localStorage.getItem("refreshToken"));
		},

		setRefreshToken: function (token: tokenType) {
			refreshToken = token;
			localStorage.setItem("refreshToken", token);
		},
		//#endregion
	};
})();

export default Auth;
