import axios, {AxiosPromise} from "axios";
import {API_ROOT} from "../helpers/constants";
import Auth from "../services/auth";
import Http from "../services/http";

const AxiosAuth = Http.init();

const Axios = axios.create({
	baseURL: API_ROOT,
});

//auth
const postSendEmail = (email: string): AxiosPromise =>
	Axios({
		method: "post",
		url: "/auth/request-login",
		data: {email},
	});

const postLogin = (token: ILoginData) => {
	return Auth.makeLogin(token);
};

//reservations
const getReservations = (query: string): AxiosPromise =>
	AxiosAuth({
		method: "get",
		url: `/reservations${query}`,
	});

const getReservation = (id: string | number): AxiosPromise =>
	AxiosAuth({
		method: "get",
		url: `/reservations/${id}`,
	});

const getVehicleBusyDates = (id: number | string): AxiosPromise =>
	AxiosAuth({
		method: "get",
		url: `/vehicles/${id}/busy-dates`,
	});

const getVehicleOffice = (id: number, params: {[key: string]: string | number}): AxiosPromise =>
	AxiosAuth({
		method: "get",
		url: `/vehicles/${id}/reservation-times`,
		params,
	});

const putReservation = (id: string | number, data: Partial<IReservation>): AxiosPromise =>
	AxiosAuth({
		method: "put",
		url: `/reservations/${id}`,
		data,
	});

const putReservationReject = (id: string | number, data: {message: string}): AxiosPromise =>
	AxiosAuth({
		method: "put",
		url: `/reservations/${id}/annul`,
		data,
	});

const postSignature = (id: string | number, file: Blob): AxiosPromise => {
	const data = new FormData();
	data.append("signature", file);
	return AxiosAuth({
		method: "post",
		url: `/reservations/${id}/signature`,
		data,
	});
};

const getReservationExtras = (id: string | number): AxiosPromise =>
	AxiosAuth({
		method: "get",
		url: `/reservations/${id}/extras`,
	});

const putReservationExtras = (id: string | number, data: extrasDataType): AxiosPromise =>
	AxiosAuth({
		method: "put",
		url: `/reservations/${id}/extras`,
		data,
	});

//account
const getDriverInfo = (): AxiosPromise => {
	return AxiosAuth({
		method: "get",
		url: "/account",
	});
};

const rsApi = {
	getDriverInfo,
	getReservation,
	getReservationExtras,
	getReservations,
	getVehicleBusyDates,
	getVehicleOffice,
	postLogin,
	postSendEmail,
	postSignature,
	putReservation,
	putReservationExtras,
	putReservationReject,
};

export default rsApi;
