import React, {useEffect} from "react";
import {useLocation} from "react-router-dom";
import {useDispatch} from "react-redux";
import {ReservationList, ListFilter, Pagination} from "../components";
import {useTypedSelector} from "../redux";
import {getReservations} from "../redux/actions";

export const Reservations: React.FC = () => {
	const {search} = useLocation();
	const {filters, data, loading} = useTypedSelector((store) => store.reservations);
	const dispatch = useDispatch();

	const {reservations, pagination} = data;

	useEffect(() => {
		dispatch(getReservations(search));
		window.scroll(0, 0);
	}, [search, dispatch]);

	return (
		<div className="page-content">
			<ListFilter total={pagination.total_count} filters={filters} />
			<ReservationList loading={loading} reservations={reservations} />
			<Pagination {...pagination} />
		</div>
	);
};
