import React from "react";

import styles from "./checkbox.module.sass";

interface ICheckbox {
	check: boolean;
	className?: string;
	disabled?: boolean;
	labelClassName?: string;
	labelContent: string | React.ReactElement;
	name: string;
	onCheck(e: React.ChangeEvent<HTMLInputElement>): void;
}

export const Checkbox: React.FC<ICheckbox> = ({
	check,
	className,
	disabled = false,
	labelClassName,
	labelContent,
	name,
	onCheck,
}) => {
	return (
		<div className={`${styles.container}${className ? ` ${className}` : ""}`}>
			<input
				className={styles.checkbox}
				type="checkbox"
				name={name}
				id={name}
				disabled={disabled}
				checked={check}
				onChange={onCheck}
			/>
			<label
				htmlFor={name}
				className={`${styles.label}${labelClassName ? ` ${labelClassName}` : ""}`}>
				{labelContent}
			</label>
		</div>
	);
};
