import React from "react";

import styles from "./billing.module.sass";

interface IDocsLinks {
	links: {
		agreement_url: string;
		invoice_url: string;
	};
}

export const DocsLinks: React.FC<IDocsLinks> = ({links}) => {
	return (
		<ul className={`${styles["links-list"]} links-list`}>
			<li>
				<a href={links.agreement_url} rel="noopener noreferrer" target="_blank">
					Agreement
				</a>
			</li>
			<li>
				<a href={links.invoice_url} rel="noopener noreferrer" target="_blank">
					Invoice
				</a>
			</li>
		</ul>
	);
};
