import React from "react";
import classnames from "classnames";

import styles from "./extras.module.sass";

interface insuranceProps extends IInsurance {
	currency: string | null;
	editable: boolean;
	onChange: (id: number) => void;
}

export const Insurance: React.FC<insuranceProps> = ({
	id,
	title,
	value,
	price,
	icon,
	deposit,
	deposit_price,
	damage,
	damage_access,
	checked = false,
	editable = false,
	currency,
	onChange,
}) => {
	const changeInsurance = (e: React.ChangeEvent<HTMLInputElement>): void => {
		onChange(id);
	};
	return (
		<div className={styles.item}>
			<div className={styles.data}>
				{editable && (
					<input
						className={styles["hidden-input"]}
						type="radio"
						name="insurance"
						defaultValue={value}
						id={String(id)}
						defaultChecked={checked}
						onChange={changeInsurance}
					/>
				)}
				<label
					className={classnames(styles.label, {
						[styles["label--radio"]]: editable,
					})}
					htmlFor={editable ? String(id) : undefined}>
					{`${title} with ${damage_access}\u00A0${currency} Excess`}
				</label>
			</div>
			<div className="price">
				{price}&nbsp;{currency}
			</div>
		</div>
	);
};
