import {useEffect, useRef} from "react";
import SignaturePad from "signature_pad";
import {getTrimmedImg, dataURItoBlob} from "../helpers/utils";

type UseSignatureProps = {
	onDraw?: () => void;
};

export const useSignature = (options?: UseSignatureProps) => {
	const signRef = useRef<HTMLCanvasElement | null>(null);
	const signaturePad = useRef<SignaturePad | null>(null);

	const clearSignature = (): void => {
		if (signaturePad.current) {
			signaturePad.current.clear();
		}
	};

	const resizeCanvas = (): void => {
		if (signRef.current) {
			signRef.current.width = signRef.current.offsetWidth || 700;
			signRef.current.height = signRef.current.offsetHeight || 400;
		}
		clearSignature();
	};

	const confirmSignature = (callback?: (signature: string, file: Blob) => void): void => {
		if (signRef.current && signaturePad.current && !signaturePad.current.isEmpty()) {
			const signature = getTrimmedImg(signRef.current);
			const file = dataURItoBlob(signature);

			callback && callback(signature, file);
		} else {
			//TODO: empty SignaturePad
		}
	};

	useEffect(() => {
		if (signRef.current) {
			signaturePad.current = new SignaturePad(signRef.current, {
				maxWidth: 1.5,
				backgroundColor: "rgba(255, 255, 255, 0)",
				penColor: "#000000",
				onBegin: options && (options.onDraw || undefined),
			});
			resizeCanvas();

			window.addEventListener("resize", resizeCanvas);
		}

		return () => {
			window.removeEventListener("resize", resizeCanvas);
		};
		// eslint-disable-next-line
	}, []);

	return {signRef, clearSignature, confirmSignature} as const;
};
