import {batch} from "react-redux";
import {
	SET_DRIVER_LOADING,
	UPDATE_DRIVER,
	DriverActionTypes,
	DriverThunkAction,
} from "../types/driver-types";
import rsApi from "../../api/rsApi";

const setDriverLoading = (loading: boolean): DriverActionTypes => {
	return {
		type: SET_DRIVER_LOADING,
		payload: loading,
	};
};

const updateDriver = (driver: IDriver): DriverActionTypes => {
	return {
		type: UPDATE_DRIVER,
		payload: driver,
	};
};

export const getDriver = (): DriverThunkAction => async (dispatch, getState) => {
	const {loading} = getState().driver;
	if (loading) return;
	dispatch(setDriverLoading(true));
	try {
		const response = await rsApi.getDriverInfo();
		if (response.data.status) {
			const {data} = response.data;
			batch(() => {
				dispatch(updateDriver(data));
				dispatch(setDriverLoading(false));
			});
		}
	} catch (error) {
		console.error(error);
		dispatch(setDriverLoading(false));
	}
};
