import {useState} from "react";

interface IUseModal {
	isOpen?: boolean;
	onOpen?: () => void;
	onClose?: () => void;
}

export const useModal = (options?: IUseModal) => {
	const {isOpen, onOpen, onClose} = options || {};
	const [modalOpen, setModalStatus] = useState(isOpen || false);

	const toggleModal = (open: boolean): void => {
		if (open !== undefined) {
			setModalStatus(open);
			open && (onOpen ? onOpen() : onClose && onClose());
		} else {
			setModalStatus((prev) => !prev);
		}
	};

	return [modalOpen, toggleModal] as const;
};
