import {
	SET_RESERVATION_LOADING,
	SET_RESERVATIONS,
	SET_RESERVATION,
	UPDATE_RESERVATION,
	UPDATE_FILTERS,
	ReservationsActionTypes,
	ReservationsState,
} from "../types/reservations-types";

const initialState: ReservationsState = {
	filters: {
		type: null,
		reserved_status: null,
		period: null,
	},
	loading: false,
	data: {
		reservations: [],
		pagination: {
			begin: 0,
			count_pages: 0,
			end: 0,
			page: 1,
			per_page: 1,
			total_count: 0,
		},
	},
	reservation: null,
};

export function reservationsReducer(
	state = initialState,
	action: ReservationsActionTypes,
): ReservationsState {
	switch (action.type) {
		case SET_RESERVATION_LOADING:
			return {
				...state,
				loading: action.payload,
			};
		case SET_RESERVATIONS:
			return {
				...state,
				data: action.payload,
			};
		case UPDATE_FILTERS:
			const newFilters: IFilters = {...state.filters};
			(Object.keys(newFilters) as Array<keyof IFilters>).forEach((key) => {
				const filterVal = action.payload[key];
				const parsedFilterVal = Array.isArray(filterVal) ? filterVal[0] : filterVal;
				newFilters[key] = parsedFilterVal ? parsedFilterVal : null;
			});
			return {
				...state,
				filters: {...state.filters, ...newFilters},
			};
		case SET_RESERVATION:
			return {
				...state,
				reservation: action.payload,
			};
		case UPDATE_RESERVATION:
			const prevReservation = {...(state.reservation as IReservation)};
			return {
				...state,
				reservation: {...prevReservation, ...action.payload},
			};
		default:
			return state;
	}
}
