import React, {useEffect} from "react";
import {useParams} from "react-router-dom";
import classnames from "classnames";
import ReactTooltip from "react-tooltip";
import {
	ActionsCard,
	ActionsCardSkeleton,
	AnnualCard,
	BillingCard,
	BillingCardSkeleton,
	DriverCard,
	ExtrasCard,
	ExtrasCardSkeleton,
	TaxCard,
	TaxesCardSkeleton,
	VehicleCard,
	VehicleCardSkeleton,
} from "../components";

import styles from "./pages.module.sass";

import {useReservation} from "../hooks";

export const Reservation: React.FC = () => {
	const {id} = useParams<{id: string}>();
	const {reservation} = useReservation(id);

	useEffect(() => {
		window.scroll(0, 0);
	}, []);

	const isReady = reservation && reservation.id === +id;

	const lowerCaseType = reservation && reservation.type.toLowerCase();
	const lowerCaseStatus = reservation && reservation.status.toLowerCase();

	const isRedjected = lowerCaseStatus === "reject" || lowerCaseStatus === "cancel";

	return (
		<div className="page-content">
			<div
				className={classnames(styles.reservation, {
					"is-rejected": isRedjected,
					"is-keyless": lowerCaseType === "keyless",
					"is-standard": lowerCaseType === "standard",
				})}>
				{/* Vehicle card */}
				{isReady ? <VehicleCard /> : <VehicleCardSkeleton />}
				{/* Driver card */}
				<DriverCard />
				{/* Extras card */}
				{isReady && (reservation as IReservation).extras_data ? (
					<>
						<ExtrasCard />
						<TaxCard />
					</>
				) : (
					<>
						<ExtrasCardSkeleton />
						<TaxesCardSkeleton />
					</>
				)}
				{isReady && (reservation as IReservation).payment_methods ? (
					<BillingCard />
				) : (
					<BillingCardSkeleton />
				)}
				{isReady ? (
					<>
						{isRedjected && <AnnualCard />}
						<ActionsCard />
					</>
				) : (
					<ActionsCardSkeleton />
				)}
				<ReactTooltip />
			</div>
		</div>
	);
};
