import React, {useState, useMemo, useRef} from "react";
import {AxiosResponse} from "axios";
import rsApi from "../../api/rsApi";
import {Checkbox} from "../";
import {LocationSelect} from "./LocationSelect";
import {LocationInput} from "./LocationInput";

import styles from "./le.module.sass";

interface ILocationEdit {
	vehicleID: number;
	initialLocations: [locationType, locationType];
	availableLocations: locationOffice[];
	sameReturn: boolean;
	customLocation: boolean;
	onChange: (s: locationType, e: locationType) => void;
}

export const LocationEdit: React.FC<ILocationEdit> = ({
	vehicleID,
	initialLocations,
	availableLocations,
	sameReturn,
	customLocation,
	onChange,
}) => {
	const [startLocation, setStartLocation] = useState(initialLocations[0]);
	const [endLocation, setEndLocation] = useState(initialLocations[1]);
	const isReturnBlocked = useRef(sameReturn);
	const isCustomAvailable = useRef(customLocation);
	const [isSystem, setIsSystem] = useState(startLocation.type === "system");
	const [isReturn, setIsReturn] = useState(sameReturn);

	const options = useMemo(
		() =>
			availableLocations.map((office_location) => ({
				value: office_location.id,
				label: office_location.title,
			})),
		//eslint-disable-next-line
		[],
	);

	const getOfficeWorkTimes = async (params: {
		[key: string]: string | number;
	}): Promise<AxiosResponse> => {
		return rsApi.getVehicleOffice(vehicleID, params);
	};

	const formatNewLocation = (
		location: locationType,
		officeID: number | null,
		officeWork: Pick<locationOffice, "work_time_enabled" | "working_time"> | null,
		system: boolean,
	) => {
		let office =
			system && officeID
				? availableLocations.filter((office) => office.id === officeID)[0]
				: location.office;
		if (
			officeWork &&
			officeWork.work_time_enabled !== undefined &&
			officeWork.working_time !== undefined
		) {
			office = {...office, ...officeWork};
		}
		return {
			...location,
			office,
			address: system && office.address ? office.address : location.address,
			lat: system && office.lat ? office.lat : location.lat,
			lng: system && office.lng ? office.lng : location.lng,
		};
	};

	const changeStartSystemLocation = async (selected: IOption): Promise<void> => {
		const selectedOfficeId = selected.value;
		if (!selectedOfficeId) return;
		let location = {...startLocation};
		try {
			const response = await getOfficeWorkTimes({office_id: +selectedOfficeId});
			location = formatNewLocation(startLocation, +selectedOfficeId, response.data.data, isSystem);
		} catch (error) {
			console.warn("Office working times are not received");
			location = formatNewLocation(startLocation, +selectedOfficeId, null, isSystem);
		}
		setStartLocation(location);
		if (isReturn) {
			setEndLocation(location);
			onChange(location, location);
		} else {
			onChange(location, endLocation);
		}
	};

	const changeEndSystemLocation = async (selected: IOption): Promise<void> => {
		const selectedOfficeId = selected.value;
		if (!selectedOfficeId || isReturn) return;
		let location = {...endLocation};
		try {
			const response = await getOfficeWorkTimes({office_id: +selectedOfficeId});
			location = formatNewLocation(endLocation, +selectedOfficeId, response.data.data, isSystem);
		} catch (error) {
			console.warn("Office working times are not received");
			location = formatNewLocation(endLocation, +selectedOfficeId, null, isSystem);
		}
		setEndLocation(location);
		onChange(startLocation, location);
	};

	const changeStartCustomLocation = async (
		lat: number,
		lng: number,
		address: string,
	): Promise<void> => {
		let location = {...startLocation, address, lat, lng};
		try {
			const response = await getOfficeWorkTimes({lat, lng, address});
			location = formatNewLocation(location, null, response.data.data, isSystem);
		} catch (error) {
			console.warn("Office working times are not received");
			location = formatNewLocation(location, null, null, isSystem);
		}
		setStartLocation(location);
		if (isReturn) {
			setEndLocation(location);
			onChange(location, location);
		} else {
			onChange(location, endLocation);
		}
	};

	const changeEndCustomLocation = async (
		lat: number,
		lng: number,
		address: string,
	): Promise<void> => {
		if (isReturn) return;
		let location = {...endLocation, address, lat, lng};
		try {
			const response = await getOfficeWorkTimes({lat, lng, address});
			location = formatNewLocation(location, null, response.data.data, isSystem);
		} catch (error) {
			console.warn("Office working times are not received");
			location = formatNewLocation(location, null, null, isSystem);
		}
		setEndLocation(location);
		onChange(startLocation, location);
	};

	const changeSystemLocation = async (e: React.ChangeEvent<HTMLInputElement>): Promise<void> => {
		setIsSystem((prevState) => !prevState);
		let sLocation = {...startLocation};
		let eLocation = {...endLocation};
		try {
			const responces = await Promise.all([
				getOfficeWorkTimes(
					!isSystem
						? {office_id: String(sLocation.office.id)}
						: {lat: sLocation.lat, lng: sLocation.lng, addres: sLocation.address},
				),
				getOfficeWorkTimes(
					!isSystem
						? {office_id: String(eLocation.office.id)}
						: {lat: eLocation.lat, lng: eLocation.lng, addres: eLocation.address},
				),
			]);
			sLocation = formatNewLocation(
				sLocation,
				sLocation.office.id,
				responces[0].data.data,
				!isSystem,
			);
			eLocation = formatNewLocation(
				eLocation,
				eLocation.office.id,
				responces[1].data.data,
				!isSystem,
			);
		} catch (error) {
			console.warn("Office working times are not received");
			sLocation = formatNewLocation(sLocation, sLocation.office.id, null, !isSystem);
			eLocation = formatNewLocation(eLocation, eLocation.office.id, null, !isSystem);
		}
		setStartLocation(sLocation);
		setEndLocation(eLocation);
		onChange(sLocation, eLocation);
	};

	const changeSameReturn = (e: React.ChangeEvent<HTMLInputElement>): void => {
		setIsReturn((prevState) => !prevState);
		onChange(startLocation, !isReturn ? startLocation : endLocation);
	};

	return (
		<div className={styles.container}>
			{isSystem ? (
				<LocationSelect
					name={"location_from"}
					value={{
						value: startLocation.office.id,
						label: startLocation.office.title,
					}}
					onChange={changeStartSystemLocation}
					options={options}
				/>
			) : (
				<LocationInput
					name={"location_from"}
					value={startLocation.address}
					onChange={changeStartCustomLocation}
				/>
			)}
			<div className="column__row">
				{isCustomAvailable.current && (
					<Checkbox
						check={!isSystem}
						labelContent="Custom location"
						labelClassName={styles.checkbox__label}
						name="custom_location"
						disabled={!isCustomAvailable.current}
						onCheck={changeSystemLocation}
					/>
				)}
				<Checkbox
					check={isReturn}
					labelContent="Same return"
					labelClassName={styles.checkbox__label}
					name="same_return"
					disabled={isReturnBlocked.current}
					onCheck={changeSameReturn}
				/>
			</div>
			{!isReturn &&
				(isSystem ? (
					<LocationSelect
						name={"location_to"}
						value={{
							value: endLocation.office.id,
							label: endLocation.office.title,
						}}
						onChange={changeEndSystemLocation}
						options={options}
					/>
				) : (
					<LocationInput
						name={"location_to"}
						value={endLocation.address}
						onChange={changeEndCustomLocation}
					/>
				))}
		</div>
	);
};
