import React from "react";

import styles from "./dc.module.sass";

interface IDriverProps {
	driver: IDriver;
}

export const Driver: React.FC<IDriverProps> = ({driver}) => {
	const {first_name, last_name} = driver; //documents here
	const documents = [
		{
			title: "Driver license",
			num: "KJ7783",
		},
		{
			title: "Passport",
			num: "KJ7783FF",
		},
	];
	return (
		<div className={styles.driver}>
			<div className={styles.driver__name}>
				{first_name} {last_name}
			</div>
			{documents.length > 0 && (
				<div className={styles.driver__docs}>
					{documents.map((document) => (
						<div className={styles.driver__doc} key={document.num}>
							<div className={styles["driver__doc-label"]}>{document.title}</div>
							<div className={styles["driver__doc-num"]}>{document.num}</div>
						</div>
					))}
				</div>
			)}
		</div>
	);
};
