import React from "react";
import classnames from "classnames";
import {useTypedSelector} from "../../../redux";
import {ucFirst} from "../../../helpers/utils";
import {InfoTip} from "../../";
import {Vehicle} from "./Vehicle";
import {LocationDateEdit} from "./LocationDateEdit";
import {LocationDateView} from "./LocationDateView";

import styles from "./vc.module.sass";

import {useMedia, useCard} from "../../../hooks";

export const VehicleCard: React.FC = () => {
	const reservation = useTypedSelector((store) => store.reservations.reservation) as IReservation;
	//is mobile?
	const media = useMedia(["(max-width: 767px)"], ["s"], "l");
	const {open, openCard, closeCard} = useCard();

	//get reservation params
	const {unique_number, status, type, vehicle, mileage_limit} = reservation;

	const renderReserveNum = (): React.ReactElement => (
		<div className={`${styles["reserve-info"]} mob-full`}>
			<div className="label">Reservation:</div>
			<div className={styles["reserve-num"]}>{unique_number}</div>
		</div>
	);

	return (
		<div className={`card ${styles.vc}`}>
			{media === "s" && renderReserveNum()}
			<Vehicle vehicle={vehicle} mileage_limit={mileage_limit} />
			<div className="column column--2">
				<div className="column__row jc-fs ai-fs column__head">
					{media === "l" && renderReserveNum()}
					<div className={styles["reserve-info"]}>
						<div className="label">Retal type:</div>
						<div className={styles["reserve-type"]}>
							{ucFirst(type)} <InfoTip text="Some text" />
						</div>
					</div>
					<div
						className={classnames(styles["reserve-info"], {
							[styles["reserve-info--status"]]: media === "l",
						})}>
						<div className="label">Status:</div>
						<div className={styles["reserve-status"]}>{ucFirst(status)}</div>
					</div>
				</div>
				{open ? (
					<LocationDateEdit reservation={reservation} onSave={closeCard} />
				) : (
					<LocationDateView reservation={reservation} changeLocation={openCard} />
				)}
			</div>
		</div>
	);
};
