import React from "react";
import {useTypedSelector} from "../../../redux/";
import {DocsLinks} from "./DocsLinks";
import {Payments} from "./Payments";
import {SignatureModal} from "./SignatureModal";

import styles from "./billing.module.sass";

export const BillingCard: React.FC = () => {
	const {agreement_url, invoice_url, currency, prices, payment_methods} = useTypedSelector(
		(store) => store.reservations.reservation,
	) as IReservation;

	return (
		<div className={`card ${styles.bc}`}>
			<div className="column column--title">
				<div className="card__title">Billing information</div>
				<DocsLinks links={{agreement_url, invoice_url}} />
			</div>
			<div className="column">
				<div className={`label-title ${styles.payment__label}`}>Payment type</div>
				<Payments methods={payment_methods} />
			</div>
			<div className="column column--last">
				<div className="column__row jc-sb ai-b">
					<div className="label-title paid-label">Paid:</div>
					<div className="price bold">
						{prices.paid}&nbsp;{currency.symbol}
					</div>
				</div>
				<div className={`column__row ${styles.sign__row}`}>
					<div className={styles.sign__place}>
						<SignatureModal />
					</div>
				</div>
			</div>
		</div>
	);
};
