import React, {useState, useEffect} from "react";
import {useDispatch} from "react-redux";
import rsApi from "../../../api/rsApi";
import {updateReservation} from "../../../redux/actions";
import {LocationEdit, Button, Map, DateEdit, RentalTime} from "../..";

import styles from "./vc.module.sass";

interface ILocationDateEdit {
	reservation: IReservation;
	onSave: () => void;
}

export const LocationDateEdit: React.FC<ILocationDateEdit> = ({reservation, onSave}) => {
	const dispatch = useDispatch();

	const [loading, setLoading] = useState(false);
	const {location_from, location_to, date_from, date_to, vehicle} = reservation;
	const [locations, setLocations] = useState<[locationType, locationType]>([
		location_from,
		location_to,
	]);
	const [dates, setDates] = useState<[string, string]>([date_from, date_to]);
	const [disabledDays, setDisabledDays] = useState([]);

	useEffect(() => {
		(async function () {
			try {
				const response = await rsApi.getVehicleBusyDates(vehicle.id);
				if (response.data.status) {
					setDisabledDays(response.data.data);
				}
			} catch (error) {
				console.error(error);
			}
		})();
	}, [vehicle.id]);

	const changeDates = (s: string, e: string): void => {
		setDates([s, e]);
	};

	const changeLocaitons = (s: locationType, e: locationType): void => {
		setLocations([s, e]);
	};

	const onSaveHandler = (e: React.MouseEvent<HTMLButtonElement>) => {
		if (loading) return;
		setLoading(true);
		const [date_from, date_to] = dates;
		const [location_from, location_to] = locations;
		dispatch(
			updateReservation(
				{
					date_from,
					date_to,
					pickup_location: location_from.address,
					return_location: location_to.address,
				},
				() => {
					setLoading(false);
					onSave();
				},
				() => {
					setLoading(false);
					//TODO: process erros for dates and locations
				},
			),
		);
	};

	return (
		<div className={`${styles["ld-edit"]} column__body`}>
			<div className="column__row">
				<div className={styles["ld-edit__dates"]}>
					<DateEdit
						initialDates={dates}
						availableTimes={[
							locations[0].office.work_time_enabled ? locations[0].office.working_time : null,
							locations[1].office.work_time_enabled ? locations[1].office.working_time : null,
						]}
						disabledDates={disabledDays}
						onChange={changeDates}
					/>
				</div>
				<div className="column">
					<LocationEdit
						vehicleID={vehicle.id}
						initialLocations={locations}
						availableLocations={vehicle.available_locations}
						sameReturn={vehicle.same_return}
						customLocation={false}
						onChange={changeLocaitons}
					/>
				</div>
			</div>
			<div className={`column__row ${styles["map-row"]}`}>
				<div className="column">
					<RentalTime date={dates[0]} />
				</div>
				<div className="column">
					<div className={styles["map-place"]}>
						<Map
							center={{
								lat: locations[0].lat,
								lng: locations[0].lng,
							}}
							marker={true}
						/>
					</div>
				</div>
			</div>
			<div className={`card__actions ${styles.actions}`}>
				<Button text="Save" size="small" onClick={onSaveHandler} loading={loading} />
			</div>
		</div>
	);
};
