export const getTrimmedImg = (c: HTMLCanvasElement): string => {
	const ctx = c.getContext("2d");
	const copy = document.createElement("canvas").getContext("2d");
	if (!ctx || !copy) return "";

	const pixels = ctx.getImageData(0, 0, c.width, c.height);
	const l = pixels.data.length;
	const bound = {
		top: 0,
		left: 0,
		right: 0,
		bottom: 0,
	};
	let i, x, y;

	// Iterate over every pixel to find the highest
	// and where it ends on every axis ()
	for (i = 0; i < l; i += 4) {
		if (pixels.data[i + 3] !== 0) {
			x = (i / 4) % c.width;
			y = ~~(i / 4 / c.width);

			if (bound.top === 0) {
				bound.top = y;
			}

			if (bound.left === 0) {
				bound.left = x;
			} else if (x < bound.left) {
				bound.left = x;
			}

			if (bound.right === 0) {
				bound.right = x;
			} else if (bound.right < x) {
				bound.right = x;
			}

			if (bound.bottom === 0) {
				bound.bottom = y;
			} else if (bound.bottom < y) {
				bound.bottom = y;
			}
		}
	}

	// Calculate the height and width of the content
	let trimHeight = bound.bottom - bound.top,
		trimWidth = bound.right - bound.left,
		trimmed = ctx.getImageData(bound.left, bound.top, trimWidth, trimHeight);

	copy.canvas.width = trimWidth;
	copy.canvas.height = trimHeight;
	copy.putImageData(trimmed, 0, 0);

	// Return trimmed canvas
	return copy.canvas.toDataURL();
};

export const dataURItoBlob = (dataURI: string): Blob => {
	// convert base64/URLEncoded data component to raw binary data held in a string
	let byteString: string = "";
	if (dataURI.split(",")[0].indexOf("base64") >= 0) {
		byteString = atob(dataURI.split(",")[1]);
	} else {
		byteString = unescape(dataURI.split(",")[1]);
	}

	// separate out the mime component
	var mimeString = dataURI.split(",")[0].split(":")[1].split(";")[0];

	// write the bytes of the string to a typed array
	var ia = new Uint8Array(byteString.length);
	for (var i = 0; i < byteString.length; i++) {
		ia[i] = byteString.charCodeAt(i);
	}

	return new Blob([ia], {type: mimeString});
};

/**
 * Make first letter Upper case
 */
export const ucFirst = (str: string): string => {
	if (!str) return str;
	return str[0].toUpperCase() + str.slice(1).toLowerCase();
};

/**
 * remove space between date and time from sting date
 */
export const unspaceDate = (date: string): string => {
	return date.replace(/\s/, "T");
};

interface IFormatDate {
	day: number;
	month: string | number;
	year: number;
	time: string;
}
/**
 * Split date for days, month, years and time
 */
export const splitDate = (date: string): IFormatDate | null => {
	if (!date) return null;
	const curDate = new Date(unspaceDate(date));
	const curHours = curDate.getHours();
	const curMinutes = curDate.getMinutes();
	return {
		day: curDate.getDate(),
		month: curDate.toLocaleString("en", {month: "short"}),
		year: curDate.getFullYear(),
		time: `${curHours < 10 ? `0${curHours}` : curHours}:${
			curMinutes < 10 ? `0${curMinutes}` : curMinutes
		}`,
	};
};

/**
 * add zero if digit < 10
 */
export const addZero = (num: number): string | number => {
	if (num < 0 || num >= 10) return num;
	return `0${num}`;
};
