import {useEffect} from "react";
import {useDispatch} from "react-redux";
import {useTypedSelector} from "../redux";
import {getReservation} from "../redux/actions";

export const useReservation = (id: string | number) => {
	const {reservation, loading} = useTypedSelector((store) => store.reservations);
	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(getReservation(+id));
	}, [id, dispatch]);

	return {
		reservation,
		loading,
	};
};
