import {batch} from "react-redux";
import {
	LOGIN,
	LOGOUT,
	SET_AUTH_LOADING,
	SET_MAIL_SENDED,
	SET_ERRORS,
	RESET_ERRORS,
	IFormFields,
	AuthActionTypes,
	AuthThunkAction,
} from "../types/auth-types";
import rsApi from "../../api/rsApi";

const setAuthLoading = (loading: boolean): AuthActionTypes => {
	return {
		type: SET_AUTH_LOADING,
		payload: loading,
	};
};

export const loginAction = (token: string): AuthThunkAction => async (dispatch, getState) => {
	const {loading} = getState().auth;
	if (loading) return;

	dispatch(setAuthLoading(true));
	try {
		await rsApi.postLogin(token);
		batch(() => {
			dispatch({type: LOGIN});
			dispatch(setAuthLoading(false));
		});
	} catch (error) {
		dispatch(setAuthLoading(false));
		window.location.href = "/login";
	}
};

const setMailSended = (status: boolean): AuthActionTypes => {
	return {
		type: SET_MAIL_SENDED,
		payload: status,
	};
};

const setErrors = (errors: IFormFields): AuthActionTypes => {
	return {
		type: SET_ERRORS,
		payload: errors,
	};
};

export const resetErrors = (name?: string): AuthActionTypes => {
	return {
		type: RESET_ERRORS,
		payload: name,
	};
};

export const sendMail = (mail: string): AuthThunkAction => async (dispatch, getState) => {
	const {loading} = getState().auth;
	if (loading) return;
	dispatch(setAuthLoading(true));

	try {
		await rsApi.postSendEmail(mail);
		batch(() => {
			dispatch(setMailSended(true));
			dispatch(setErrors({}));
			dispatch(setAuthLoading(false));
		});
	} catch (error) {
		const {message} = error.response.data.data;
		batch(() => {
			dispatch(setMailSended(false));
			dispatch(setAuthLoading(false));
			if (message) {
				dispatch(
					setErrors({
						email: message,
					}),
				);
			}
		});
	}
};

export const LogoutAction = (): AuthActionTypes => {
	return {
		type: LOGOUT,
	};
};
