import {useHistory, useLocation} from "react-router-dom";
import queryString, {ParsedQuery} from "query-string";

type IParams = {
	name: string;
	value: string | number | undefined | null;
};

export const useSearch = () => {
	const {search} = useLocation();
	const {push} = useHistory();

	const formParamsObject = (params: IParams[]): ParsedQuery => {
		let newSearch = {};
		params.forEach(({name, value}) => {
			newSearch = {...newSearch, [name]: value};
		});

		return newSearch;
	};

	const updateLocationSearch = (params: IParams[]): void => {
		const searchParams = queryString.parse(search);
		const newSearch = formParamsObject(params);
		push({
			search: queryString.stringify({...searchParams, ...newSearch}),
		});
	};

	const setLocationSearch = (params: IParams[]): void => {
		const newSearch = formParamsObject(params);
		push({
			search: queryString.stringify(newSearch),
		});
	};

	return {
		setLocationSearch,
		updateLocationSearch,
	};
};
