import {useDispatch} from "react-redux";

import {updateExtras} from "../redux/actions/";

export const useExtras = (reservation: IReservation, editMode: boolean) => {
	const dispatch = useDispatch();

	const {extras_data, currency} = reservation;

	const insurances = editMode
		? [...extras_data.insurances]
		: extras_data.insurances.filter((i: IInsurance) => i.checked === true);
	const extras = editMode
		? [...extras_data.extras]
		: extras_data.extras.filter((e: IExtra) => e.quantity > 0);

	const changeInsurance = (id: number) => {
		dispatch(
			updateExtras(
				insurances.map((insurance: IInsurance) => {
					insurance.checked = insurance.id === id;
					return insurance;
				}),
				extras,
			),
		);
	};

	const changeExtras = (id: number, quantity: number) => {
		dispatch(
			updateExtras(
				insurances,
				extras.map((extra: IExtra) => {
					if (extra.id === id) extra.quantity = quantity;
					return extra;
				}),
			),
		);
	};

	return {
		insurances,
		extras,
		currency,
		changeInsurance,
		changeExtras,
	} as const;
};
