import {ThunkAction} from "redux-thunk";
import {RootState} from "..";

export const SET_DRIVER_LOADING = "SET_DRIVER_LOADING";
export const UPDATE_DRIVER = "UPDATE_DRIVER";

export interface DriverState {
	loading: boolean;
	data: IDriver;
}

type SetDriverLoadingAction = {
	type: typeof SET_DRIVER_LOADING;
	payload: boolean;
};

type SetDriverActionType = {
	type: typeof UPDATE_DRIVER;
	payload: IDriver;
};

export type DriverActionTypes = SetDriverLoadingAction | SetDriverActionType;

export type DriverThunkAction = ThunkAction<Promise<void>, RootState, unknown, DriverActionTypes>;
