import {useState} from "react";

export const useLocaionView = ({location_from, location_to}: IReservation) => {
	const [showReturn, setMapReturn] = useState(false);

	const {lat, lng} = showReturn ? location_to : location_from;

	const showPickupLocation = (): void => {
		setMapReturn(false);
	};

	const showReturnLocation = (): void => {
		setMapReturn(true);
	};

	return {
		mapCenter: lat && lng ? {lat, lng} : undefined,
		showPickupLocation,
		showReturnLocation,
	};
};
