import React from "react";
import formatDistanceStrict from "date-fns/formatDistanceStrict";
import {unspaceDate} from "../../../helpers//utils";
// import {Button} from "../../../components";
import {Driver} from "./Driver";
import {DriverCardSkeleton, RentalPeriodSkeleton} from "../../";

import styles from "./dc.module.sass";

interface IClosedDriverCard {
	// changeDriver: () => void;
	drivers: IDriver[];
	reservation: IReservation | null;
}

export const ClosedDriverCard: React.FC<IClosedDriverCard> = ({drivers, reservation}) => {
	// const openCardHandler = (e: React.MouseEvent<HTMLButtonElement>): void => {
	// 	changeDriver();
	// };

	return (
		<div className={`card ${styles.dc}`}>
			<div className="column column--title">
				<div className="column__row ai-c">
					<span className="card__title">Drivers</span>
					{/* <Button text="Change" size="small" onClick={() => {}} /> */}
				</div>
			</div>
			<div className={`column ${styles["column--drivers"]}`}>
				{!drivers[0].first_name ? (
					<DriverCardSkeleton />
				) : (
					drivers.map((driver, index) => <Driver key={index} driver={driver} />)
				)}
			</div>
			<div className="column column--last">
				{reservation ? (
					<>
						<div className="label-title">
							Rental period{" "}
							{formatDistanceStrict(
								new Date(unspaceDate(reservation.date_to)),
								new Date(unspaceDate(reservation.date_from)),
								{
									unit: "day",
									roundingMethod: "ceil",
								},
							)}
						</div>
						<div className="price">
							{reservation.prices.vehicle}&nbsp;{reservation.currency.symbol}
						</div>
					</>
				) : (
					<RentalPeriodSkeleton />
				)}
			</div>
		</div>
	);
};
