import React from "react";

import styles from "./vp.module.sass";

interface IVehicleParam {
	icon: string;
	value: string | number;
	isTransmission?: boolean;
}

export const VehicleParam: React.FC<IVehicleParam> = ({icon, value, isTransmission = false}) => {
	let matches = null;
	if (isTransmission) {
		matches = String(value).match(/^([\s\S]*?)\s*\(([\s\S]*?)\)/i);
	}
	return (
		<div className={styles.param} data-tip={matches ? matches[1] : null}>
			<i className={`fico fico-${icon}`} />
			{matches ? matches[2] : value}
		</div>
	);
};
