import React from "react";
import classnames from "classnames";
import formatDistanceToNow from "date-fns/formatDistanceToNow";
import isPast from "date-fns/isPast";
import {unspaceDate} from "../../helpers/utils";

import styles from "./rt.module.sass";

interface IRentalTime {
	date: string;
	className?: string;
}

export const RentalTime: React.FC<IRentalTime> = ({date, className}) => {
	const isDate = !!date;
	const isBegan = isDate ? isPast(new Date(unspaceDate(date))) : false;

	return (
		<div className={className}>
			<div className="label">{isBegan ? "Your order has started: " : "Time to start rental: "}</div>
			<div
				className={classnames(`${styles["time-left"]} time-left`, {
					"time-left--red": isBegan,
				})}>
				{isDate ? formatDistanceToNow(new Date(unspaceDate(date)), {addSuffix: true}) : "---"}
			</div>
		</div>
	);
};
