import React, {useState, useEffect} from "react";
import DatePicker, {registerLocale} from "react-datepicker";
import {isEqual} from "underscore";
import enGB from "date-fns/locale/en-GB";
import setHours from "date-fns/setHours";
import setMinutes from "date-fns/setMinutes";
import "react-datepicker/dist/react-datepicker.css";
import {addZero, unspaceDate} from "../../helpers/utils";
import {DateInput} from "./DateInput";
import styles from "./de.module.sass";
registerLocale("en-GB", enGB);

type pickerDateType = Date | undefined;

type disabledDate = {
	from: string;
	to: string;
};

interface IDateEdit {
	initialDates: [string, string];
	availableTimes: [workingType[] | null, workingType[] | null];
	disabledDates: disabledDate[];
	onChange: (s: string, e: string) => void;
}

export const DateEdit: React.FC<IDateEdit> = ({
	initialDates,
	availableTimes,
	disabledDates,
	onChange,
}) => {
	const [weekT, setWeekT] = useState(availableTimes);

	const formatTime = (day: Date, time: string): Date => {
		const [hours, minustes] = time.split(":");
		return setHours(setMinutes(day, +minustes), +hours);
	};

	const formatChangedDate = (date: pickerDateType): string => {
		if (!date) return "";
		return `${date.getFullYear()}-${addZero(date.getMonth() + 1)}-${addZero(
			date.getDate(),
		)} ${addZero(date.getHours())}:${addZero(date.getMinutes())}`;
	};

	const getDayTime = (day: pickerDateType, path: 0 | 1, period: "from" | "to"): pickerDateType => {
		if (!day) return;
		const weekDay = (day.getDay() + 6) % 7;
		const weekTimes = availableTimes[path];
		if (weekTimes) {
			const dayPeriodTime = weekTimes[weekDay][period];
			if (dayPeriodTime) {
				return formatTime(day, dayPeriodTime);
			} else {
				return setHours(setMinutes(day, 0), 0);
			}
		}
		return;
	};

	const getWorkingDate = (
		date: Date,
		path: 0 | 1,
	): {
		min: pickerDateType;
		max: pickerDateType;
		selected: Date;
	} => {
		let newDate: Date = date;
		const newMinTime = getDayTime(date, path, "from");
		const newMaxTime = getDayTime(date, path, "to");
		if (newMinTime && newMinTime.getTime() > date.getTime()) {
			newDate = newMinTime;
		} else if (newMaxTime && newMaxTime.getTime() < date.getTime()) {
			newDate = newMaxTime;
		}

		return {
			min: newMinTime,
			max: newMaxTime,
			selected: newDate,
		};
	};

	const [startDate, setStartDate] = useState<pickerDateType>(
		new Date(unspaceDate(initialDates[0])),
	);
	const [endDate, setEndDate] = useState<pickerDateType>(new Date(unspaceDate(initialDates[1])));
	const [minTimeFrom, setMinTimeFrom] = useState<pickerDateType>(() =>
		getDayTime(startDate, 0, "from"),
	);
	const [maxTimeFrom, setMaxTimeFrom] = useState<pickerDateType>(() =>
		getDayTime(startDate, 0, "to"),
	);
	const [minTimeTo, setMinTimeTo] = useState<pickerDateType>(() => getDayTime(endDate, 1, "from"));
	const [maxTimeTo, setMaxTimeTo] = useState<pickerDateType>(() => getDayTime(endDate, 1, "to"));

	const checkRangeInDisabled = (dateFrom: pickerDateType, dateTo: pickerDateType): boolean => {
		if (!dateFrom || !dateTo) return false;
		const dateFromTime = dateFrom.getTime();
		const dateToTime = dateTo.getTime();
		for (let i = 0; i < disabledDates.length; i++) {
			const dFromTime = new Date(unspaceDate(disabledDates[i].from)).getTime();
			const dToTime = new Date(unspaceDate(disabledDates[i].to)).getTime();
			if (dateFromTime > dFromTime && dateFromTime <= dToTime) return true;
			if (dateToTime > dFromTime && dateToTime <= dToTime) return true;
			if (dFromTime > dateFromTime && dateToTime >= dToTime) return true;
		}
		return false;
	};

	const fromPickerChange = (date: Date): void => {
		const workingDate = getWorkingDate(date, 0);
		if (
			workingDate.min &&
			workingDate.max &&
			workingDate.min.getTime() === workingDate.max.getTime()
		)
			return;
		if (
			(endDate && workingDate.selected.getTime() > endDate.getTime()) ||
			checkRangeInDisabled(workingDate.selected, endDate)
		) {
			setEndDate(undefined);
		}
		setStartDate(workingDate.selected);
		setMinTimeFrom(workingDate.min);
		setMaxTimeFrom(workingDate.max);
		onChange(formatChangedDate(workingDate.selected), formatChangedDate(endDate));
	};

	const toPickerChange = (date: Date): void => {
		const workingDate = getWorkingDate(date, 1);
		if (
			workingDate.min &&
			workingDate.max &&
			workingDate.min.getTime() === workingDate.max.getTime()
		)
			return;
		if (
			(startDate && workingDate.selected.getTime() <= startDate.getTime()) ||
			checkRangeInDisabled(startDate, workingDate.selected)
		)
			return;
		setEndDate(workingDate.selected);
		setMinTimeTo(workingDate.min);
		setMaxTimeTo(workingDate.max);
		onChange(formatChangedDate(startDate), formatChangedDate(workingDate.selected));
	};

	const excludeDisabled = (date: Date): boolean => {
		const dateTime = date.getTime();
		if (!disabledDates.length) return true;
		for (let i = 0; i < disabledDates.length; i++) {
			const dFromTime = new Date(unspaceDate(disabledDates[i].from)).getTime();
			const dToTime = new Date(unspaceDate(disabledDates[i].to)).getTime();
			if (dateTime >= dFromTime && dateTime <= dToTime) return false;
		}
		return true;
	};

	const updateStartLimits = () => {
		if (startDate) {
			const fromWorkingDate = getWorkingDate(startDate, 0);
			setStartDate(undefined);
			setMinTimeFrom(fromWorkingDate.min);
			setMaxTimeFrom(fromWorkingDate.max);
		}
	};

	const updateEndLimits = () => {
		if (endDate) {
			const toWorkingDate = getWorkingDate(endDate, 1);
			setEndDate(undefined);
			setMinTimeTo(toWorkingDate.min);
			setMaxTimeTo(toWorkingDate.max);
		}
	};

	useEffect(() => {
		if (!isEqual(availableTimes[0], weekT[0])) {
			setWeekT((prevAvailable) => [availableTimes[0], prevAvailable[1]]);
			updateStartLimits();
		}

		if (!isEqual(availableTimes[1], weekT[1])) {
			setWeekT((prevAvailable) => [prevAvailable[0], availableTimes[1]]);
			updateEndLimits();
		}
		//eslint-disable-next-line
	}, [availableTimes]);

	return (
		<div className={styles["container"]}>
			<div className={styles["item"]}>
				<div className="label">Pick Up</div>
				<DatePicker
					onChange={(newStartDate: Date) => fromPickerChange(newStartDate)}
					selected={startDate}
					startDate={startDate}
					endDate={endDate}
					filterDate={excludeDisabled}
					selectsStart
					showTimeSelect
					timeFormat="HH:mm"
					timeIntervals={15}
					minTime={minTimeFrom}
					maxTime={maxTimeFrom}
					//@ts-ignore
					customInput={<DateInput />}
					dateFormat="MMM | dd | yyyy || HH:mm"
					locale="en-GB"
				/>
			</div>
			<div className={styles["item"]}>
				<div className="label">Return</div>
				<DatePicker
					onChange={(newEndDate: Date) => toPickerChange(newEndDate)}
					selected={endDate}
					startDate={startDate}
					minDate={startDate}
					endDate={endDate}
					filterDate={excludeDisabled}
					selectsEnd
					showTimeSelect
					timeFormat="HH:mm"
					timeIntervals={15}
					minTime={minTimeTo}
					maxTime={maxTimeTo}
					//@ts-ignore
					customInput={<DateInput />}
					dateFormat="MMM | dd | yyyy || HH:mm"
					locale="en-GB"
				/>
			</div>
		</div>
	);
};
