import React from "react";
import {ReservationListItem} from "./ReservationListItem";
import {ReservationListSkeleton} from "../";

import styles from "./filters.module.sass";

interface IReservationList {
	loading: boolean;
	reservations: IReservation[];
}

export const ReservationList: React.FC<IReservationList> = ({loading = false, reservations}) => {
	return (
		<div className={styles.list}>
			{loading && reservations.length === 0 ? (
				<ReservationListSkeleton />
			) : (
				reservations.map((reservation: IReservation) => (
					<ReservationListItem key={reservation.id} reservation={reservation} loading={loading} />
				))
			)}
		</div>
	);
};
