import React from "react";
import {splitDate} from "../../helpers/utils";

import styles from "./dl.module.sass";

interface IDateLocation {
	date: string;
	location: string;
	onLocationClick?: () => void;
}

export const DateLocation: React.FC<IDateLocation> = ({date, location, onLocationClick}) => {
	const formatedDate = splitDate(date);
	if (!formatedDate) return null;
	const locationClickHandler = (e: React.MouseEvent) => {
		onLocationClick && onLocationClick();
	};
	return (
		<div className={styles.container}>
			<div className={styles.date}>
				<div className={styles.month}>{formatedDate.month}</div>
				<div className={styles.day}>{formatedDate.day}</div>
				<div className={styles.year}>{formatedDate.year}</div>
			</div>
			<div>
				<div className={styles.location} onClick={locationClickHandler}>
					{location}
				</div>
				<div className={styles.time}>{formatedDate.time}</div>
			</div>
		</div>
	);
};
