import React from "react";
import Skeleton from "react-loading-skeleton";

import styles from "../Reservation/ExtrasCard/extras.module.sass";

export const ExtrasCardSkeleton: React.FC = () => {
	return (
		<div className={`card ${styles.card}`}>
			<div className="column column--title">
				<div className="column__row ai-c">
					<span className="card__title">Extras</span>
				</div>
			</div>
			<div className="column column--2">
				<Skeleton width={65} height={12} style={{display: "block", marginBottom: "7px"}} />
				<div>
					<div className={styles.item}>
						<div className={styles.data}>
							<Skeleton width={170} height={18} style={{display: "block"}} />
						</div>
						<Skeleton width={70} height={18} />
					</div>
				</div>
				<Skeleton
					width={65}
					height={12}
					style={{display: "block", marginBottom: "7px", marginTop: "20px"}}
				/>
				<div>
					<div className={styles.item}>
						<div className={styles.data}>
							<Skeleton width={170} height={18} style={{display: "block"}} />
						</div>
						<Skeleton width={70} height={18} />
					</div>
				</div>
			</div>
		</div>
	);
};
