import React from "react";
import ReactPaginate from "react-paginate";

import styles from "./pagination.module.sass";

import {useSearch} from "../../hooks";

export const Pagination: React.FC<IPagination> = ({count_pages, per_page, page}) => {
	const {updateLocationSearch} = useSearch();

	const onChangePage = (selectedItem: {selected: number}): void => {
		updateLocationSearch([
			{
				name: "page",
				value: ++selectedItem.selected,
			},
			{
				name: "per_page",
				value: per_page,
			},
		]);
	};

	return count_pages ? (
		<div className={styles.pagination}>
			<ReactPaginate
				previousLabel={"previous"}
				nextLabel={"next"}
				breakLabel={"..."}
				breakClassName={"break-me"}
				pageCount={count_pages}
				forcePage={--page}
				marginPagesDisplayed={2}
				pageRangeDisplayed={5}
				onPageChange={onChangePage}
				containerClassName={"pagination"}
				activeClassName={"active"}
			/>
		</div>
	) : null;
};
