import React from "react";
import {useHistory} from "react-router-dom";
import {useTypedSelector} from "../../../redux/";
import {Button, Todo} from "../../";

import styles from "./actions.module.sass";

export const ActionsCard: React.FC = () => {
	const reservation = useTypedSelector((store) => store.reservations.reservation) as IReservation;
	const {status, type} = reservation;
	const lowerCaseType = type.toLowerCase();
	const lowerCaseStatus = status.toLowerCase();

	const isRedjected = lowerCaseStatus === "reject" || lowerCaseStatus === "cancel";

	const history = useHistory();

	const backHandler = (e: React.MouseEvent<HTMLButtonElement>): void => {
		history.goBack();
	};

	return (
		<div className="card">
			<div className="column">
				{/* <LinksList>
					<li>
						<a href="!#" target="_blank">
							How «standard rental» works?
						</a>
					</li>
					<li>
						<a href="!#" target="_blank">
							Customer service
						</a>
					</li>
				</LinksList> */}
			</div>
			<div className="column column--2">
				<div className={`column__row jc-fe ai-fs ${styles.btns}`}>
					<Button text="Back" onClick={backHandler} />
					{!isRedjected && lowerCaseStatus !== "done" && (
						<>
							<Todo className={styles.todo} actions={reservation.necessary_actions} />
							<Button
								text="Confirm"
								color={
									lowerCaseType === "keyless"
										? "green"
										: lowerCaseType === "standard"
										? "blue"
										: "black"
								}
								onClick={backHandler}
							/>
						</>
					)}
				</div>
			</div>
		</div>
	);
};
