import React from "react";
import Modal from "react-modal";
import Skeleton from "react-loading-skeleton";
import {useDispatch} from "react-redux";
import {useTypedSelector} from "../../../redux";
import {uploadSignature} from "../../../redux/actions";
import {Signature} from "../../";

import stylesSign from "../../Signature/signature.module.sass";
import styles from "./billing.module.sass";

import {useModal} from "../../../hooks";

Modal.setAppElement("#rentsyst_personal");

export const SignatureModal: React.FC = () => {
	const {reservation, loading} = useTypedSelector((store) => store.reservations);
	const src = reservation && reservation.signature.url ? reservation.signature.url : "";

	const lowerCaseStatus = reservation && reservation.status.toLowerCase();
	const isRedjected = lowerCaseStatus === "reject" || lowerCaseStatus === "cancel";

	const [modalOpen, toggleModal] = useModal();

	const dispatch = useDispatch();

	const openSignModalHandler = (e: React.MouseEvent<HTMLButtonElement>): void => {
		toggleModal(true);
	};

	const closeModalHandler = () => {
		toggleModal(false);
	};

	const onSignatureConfirm = (signature: string, file: Blob): void => {
		toggleModal(false);
		dispatch(uploadSignature(signature, file));
	};

	return (
		<div className={styles.sign__modal}>
			{src ? (
				<img className={stylesSign.sign__preview} src={src} alt="" />
			) : (
				<div className={styles.sign__create}>
					<i className="fico fico-contract" />
					<span className={styles["sign__create-text"]}>Sign an agreement</span>
				</div>
			)}
			{loading ? (
				<Skeleton width={"100%"} height={"100%"} className={styles["sign__btn-skelet"]} />
			) : (
				!isRedjected &&
				lowerCaseStatus !== "done" && (
					<button type="button" className={styles.sign__btn} onClick={openSignModalHandler} />
				)
			)}
			<Modal
				className={`rs-modal rs-modal--lg ${styles.sign__content}`}
				isOpen={modalOpen}
				contentLabel="Sign Modal"
				onRequestClose={closeModalHandler}>
				<div className="sign-modal__content">
					<div className="rs-modal__header">
						<span className="rs-modal__title">Signature</span>
						<button className="rs-modal__close" onClick={closeModalHandler}>
							<span>×</span>
						</button>
					</div>
					<div className="rs-modal__body">
						<Signature preview={src} onConfirm={onSignatureConfirm} />
					</div>
				</div>
			</Modal>
		</div>
	);
};
