import React from "react";
import {Select} from "../../components";

import styles from "./le.module.sass";

type ILocationSelect = {
	name: string;
	value: IOption;
	onChange: (selected: IOption) => void;
	options: IOption[];
};

export const LocationSelect: React.FC<ILocationSelect> = ({name, value, onChange, options}) => {
	return (
		<div className={`${styles["control-wrap"]} ${styles["control-wrap--select"]}`}>
			<i className="fico fico-place" />
			<Select
				className={styles.select}
				name={name}
				options={options}
				value={value}
				placeholder="Choose location"
				onChange={onChange}
			/>
		</div>
	);
};
