import React from "react";
import {VehicleParam} from "../../";

import vpStyles from "../../VehicleParam/vp.module.sass";
import styles from "./vc.module.sass";

interface IVehicleProps {
	vehicle: IVehicle;
	mileage_limit: string;
}

export const Vehicle: React.FC<IVehicleProps> = ({vehicle, mileage_limit}) => {
	const {large_bags, number_seats, refill, transmission, group, brand, mark, thumbnail} = vehicle;
	return (
		<div className={`column ${styles.vehicle}`}>
			<div className={styles.vehicle__info}>
				<div className={styles.vehicle__img}>
					<img src={thumbnail} alt={`${brand} ${mark}`} />
				</div>
				<div className={styles.vehicle__group}>{group}</div>
				<span className={styles.vehicle__title}>
					{`${brand} ${mark} `}
					<span className={styles.vehicle__similar}>or similar</span>
				</span>
			</div>
			<div className={styles.vehicle__info}>
				{(transmission.name || large_bags || number_seats) && (
					<div className={vpStyles.vp__group}>
						{transmission.name && (
							<VehicleParam icon="transmission" value={transmission.name} isTransmission />
						)}
						{number_seats && <VehicleParam icon="driver" value={number_seats} />}
						{large_bags && <VehicleParam icon="profile" value={large_bags} />}
					</div>
				)}
				{mileage_limit && <VehicleParam icon="gauge" value={mileage_limit} />}
				{refill && <VehicleParam icon="gas-station2" value={refill} />}
			</div>
		</div>
	);
};
