import {ThunkAction} from "redux-thunk";
import {RootState} from "../";

export const LOGIN = "LOGIN";
export const LOGOUT = "LOGOUT";
export const SET_AUTH_LOADING = "SET_AUTH_LOADING";
export const SET_MAIL_SENDED = "SET_MAIL_SENDED";
export const SET_ERRORS = "SET_ERRORS";
export const RESET_ERRORS = "RESET_ERRORS";

type LoginType = {
	type: typeof LOGIN;
};

type LogoutType = {
	type: typeof LOGOUT;
};
export interface IFormFields {
	[key: string]: string;
}
export interface AuthState {
	isLoggedIn: boolean;
	loading: boolean;
	mailIsSended: boolean;
	errors: IFormFields;
}

type SetAuthLoadingActionType = {
	type: typeof SET_AUTH_LOADING;
	payload: boolean;
};

type SetMailSendedActionType = {
	type: typeof SET_MAIL_SENDED;
	payload: boolean;
};

type setErrorsType = {
	type: typeof SET_ERRORS;
	payload: IFormFields;
};

type resetErrorsType = {
	type: typeof RESET_ERRORS;
	payload?: string;
};

export type AuthActionTypes =
	| LoginType
	| LogoutType
	| SetAuthLoadingActionType
	| SetMailSendedActionType
	| setErrorsType
	| resetErrorsType;

export type AuthThunkAction = ThunkAction<Promise<void>, RootState, unknown, AuthActionTypes>;
