import React, {useEffect, useRef} from "react";

interface IMap {
	center?: {
		lat: number;
		lng: number;
	};
	marker?: boolean;
}

export const Map: React.FC<IMap> = ({center, marker = false}) => {
	const mapNodeRef: {current: HTMLDivElement | null} = useRef(null);
	let mapRef: {current: google.maps.Map | null} = useRef(null);
	let markerRef: {current: google.maps.Marker | null} = useRef(null);

	useEffect(() => {
		if (mapNodeRef.current === null) return;
		mapRef.current = new google.maps.Map(mapNodeRef.current as HTMLDivElement, {
			zoom: 15,
			center: {lat: 0, lng: 0},
		});
		if (marker) {
			markerRef.current = new google.maps.Marker({
				position: {lat: 0, lng: 0},
			});
			markerRef.current.setMap(mapRef.current);
		}
		// eslint-disable-next-line
	}, []);

	useEffect(() => {
		if (mapRef.current === null || !center) return;
		mapRef.current.setCenter(center);
		if (marker && markerRef.current) {
			markerRef.current.setPosition(center);
		}
	}, [center, marker]);

	return center ? <div ref={mapNodeRef} style={{width: "100%", height: "100%"}} /> : null;
};
