import React from "react";
import {useTypedSelector} from "../../../redux";

import styles from "./annul.module.sass";

export const AnnualCard: React.FC = () => {
	const {annul} = useTypedSelector((store) => store.reservations.reservation) as IReservation;
	return annul ? (
		<div className="card">
			<div className="column">
				<div className={`card__title ${styles.annual__title}`}>Annul</div>
			</div>
			<div className={`column column--2 ${styles["text-column"]}`}>
				<p className={styles.annual__text}>{annul}</p>
			</div>
		</div>
	) : null;
};
