import React from "react";
import {Select} from "../";

import styles from "./filters.module.sass";

import {useSearch} from "../../hooks";

interface IListFilter {
	filters: IFilters;
	total?: number;
}

type filterKey = keyof IFilters;

type ISelects = {
	[key in filterKey]: {
		options: IOption[];
		placeholder: string;
	};
};

const selects: ISelects = {
	type: {
		options: [
			{value: "", label: "All"},
			{value: "keyless", label: "Keyless"},
			{value: "standard", label: "Standard"},
		],
		placeholder: "Rental type",
	},
	reserved_status: {
		options: [
			{value: "", label: "All"},
			{value: "booking", label: "Booking"},
			{value: "reserved", label: "Reserved"},
			{value: "rental", label: "Rental"},
			{value: "done", label: "Done"},
			{value: "cancel", label: "Cancel"},
			{value: "reject", label: "Reject"},
		],
		placeholder: "Status",
	},
	period: {
		options: [
			{value: "", label: "All"},
			{value: "future", label: "Future"},
			{value: "past", label: "Past"},
		],
		placeholder: "",
	},
};

export const ListFilter: React.FC<IListFilter> = ({filters, total}) => {
	const {updateLocationSearch} = useSearch();

	const getTotalText = (num?: number): string => {
		switch (num) {
			case 0:
			case undefined:
				return "Not found";
			case 1:
				return "1 Reservation";
			default:
				return `${num} Reservations`;
		}
	};

	const onFilterChange = (selected: IOption, name: filterKey) => {
		updateLocationSearch([
			{
				name,
				value: selected.value,
			},
		]);
	};

	return (
		<div className={styles.container}>
			<span className={styles.total}>{getTotalText(total)}</span>
			<div className={styles.filters}>
				{(Object.keys(selects) as Array<filterKey>).map((name) => {
					const {options, placeholder} = selects[name];
					return (
						<Select
							key={name}
							name={name}
							options={options}
							value={options.filter((t) => t.value === filters[name])[0] || null}
							placeholder={placeholder}
							onChange={onFilterChange}
						/>
					);
				})}
			</div>
		</div>
	);
};
