import React from "react";
import classnames from "classnames";
import Skeleton from "react-loading-skeleton";

import styles from "./button.module.sass";

interface IButton {
	className?: string;
	text: string;
	color?: "black" | "green" | "blue";
	onClick?(e: React.MouseEvent<HTMLButtonElement>): void;
	size?: "small";
	disabled?: boolean;
	type?: "button" | "submit";
	loading?: boolean;
}

export const Button: React.FC<IButton> = ({
	className = "",
	text,
	color = "",
	size,
	onClick,
	disabled = false,
	type = "button",
	loading = false,
}) => {
	return (
		<button
			className={classnames(styles.button, {
				[styles.small]: size === "small",
				[styles[color]]: !!color,
				[className]: !!className,
			})}
			type={type}
			onClick={onClick}
			disabled={disabled}>
			{text}
			{loading && <Skeleton className={styles.button__loader} />}
		</button>
	);
};
