import {useState} from "react";

type IProps = {
	isOpen?: boolean;
	onOpen?: () => void;
	onClose?: () => void;
};

export const useCard = (options?: IProps) => {
	const {isOpen = false, onOpen, onClose} = options || {};
	const [open, setToggle] = useState(isOpen);

	const toggleCard = (open?: boolean): void => {
		if (open !== undefined) {
			setToggle(open);
			open && (onOpen ? onOpen() : onClose && onClose());
		} else {
			setToggle((prev) => !prev);
		}
	};

	const openCard = () => {
		setToggle(true);
		onOpen && onOpen();
	};

	const closeCard = () => {
		setToggle(false);
		onClose && onClose();
	};

	return {open, toggleCard, openCard, closeCard} as const;
};
