import React from "react";
import ReactSelect, {ValueType, components, SingleValueProps, OptionProps} from "react-select";

import styles from "./select.module.sass";

type OptionType = {value: string | number | null; label: string};

type OptionsType = OptionType[];

interface ISelect {
	className?: string;
	id?: string;
	isSearchable?: boolean;
	name?: string;
	onChange(selected?: ValueType<OptionType | OptionsType>, name?: string): void;
	options: OptionsType;
	placeholder?: string;
	value?: ValueType<OptionType | OptionsType>;
	withIcon?: boolean;
}

export const Select: React.FC<ISelect> = ({
	className = "",
	id,
	isSearchable = false,
	name,
	onChange,
	options,
	placeholder = "",
	value,
	withIcon = false,
}) => {
	const CustomSingleValue: React.ComponentType<SingleValueProps<any>> = (props) => (
		<components.SingleValue {...props}>
			<img src={props.data.icon} alt="" />
			{props.data.label}
		</components.SingleValue>
	);

	const CustomOption: React.ComponentType<OptionProps<any>> = (props) => (
		<components.Option {...props}>
			<img src={props.data.icon} alt="" />
			{props.data.label}
		</components.Option>
	);

	const onSelectChange = (selected?: ValueType<OptionType | OptionsType>): void => {
		onChange(selected, name);
	};
	return (
		<ReactSelect
			className={`${styles.select} ${className}`}
			classNamePrefix="rs"
			isSearchable={isSearchable}
			onChange={onSelectChange}
			components={withIcon ? {SingleValue: CustomSingleValue, Option: CustomOption} : undefined}
			options={options}
			placeholder={placeholder}
			value={value}
			inputProps={{
				id: id,
			}}
		/>
	);
};
