import {useState} from "react";
import {useDispatch, shallowEqual} from "react-redux";
import {useTypedSelector} from "../redux";
import {sendMail, resetErrors} from "../redux/actions";
import {IFormFields} from "../redux/types/auth-types";

export const useLoginForm = (fields: IFormFields) => {
	const [values, setValues] = useState(fields);
	const {loading, errors} = useTypedSelector((store) => store.auth, shallowEqual);

	const dispatch = useDispatch();

	const onSubmitForm = (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		dispatch(sendMail(values.email));
	};

	const onValueChange = (e: React.ChangeEvent<HTMLInputElement>) => {
		const {name, value} = e.target;
		setValues((prevValues) => ({
			...prevValues,
			[name]: value,
		}));
	};

	const onFocus = (e: React.FocusEvent<HTMLInputElement>) => {
		dispatch(resetErrors(e.currentTarget.name));
	};

	return {loading, values, errors, onSubmitForm, onValueChange, onFocus};
};
