import React from "react";
import Skeleton from "react-loading-skeleton";

import styles from "../Reservation/BillingCard/billing.module.sass";

export const BillingCardSkeleton: React.FC = () => {
	return (
		<div className={`card ${styles.bc}`}>
			<div className="column column--title">
				<div className="card__title">Billing information</div>
				<Skeleton width={70} height={14} style={{display: "block", marginTop: "15px"}} />
				<Skeleton width={70} height={14} style={{display: "block", marginTop: "14px"}} />
			</div>
			<div className="column">
				<Skeleton width={90} height={12} style={{display: "block", marginBottom: "7px"}} />
				<Skeleton width={183} height={38} />
			</div>
			<div className="column column--last">
				<div className="column__row jc-sb ai-b">
					<Skeleton width={30} height={12} style={{marginBottom: "12px"}} />
					<Skeleton width={100} height={18} />
				</div>
				<div className={`column__row ${styles.sign__row}`}>
					<div className={styles.sign__place}>
						<Skeleton width="100%" height="100%" />
					</div>
				</div>
			</div>
		</div>
	);
};
