import {
	SET_DRIVER_LOADING,
	UPDATE_DRIVER,
	DriverState,
	DriverActionTypes,
} from "../types/driver-types";

const initialState: DriverState = {
	loading: false,
	data: {
		first_name: "",
		last_name: null,
		email: "",
		phone: null,
		birthday: null,
		country: null,
		zip: null,
		state: null,
		city: null,
		street: null,
		building: null,
		documents: [],
	},
};

export function driverReducer(state = initialState, action: DriverActionTypes): DriverState {
	switch (action.type) {
		case SET_DRIVER_LOADING:
			return {
				...state,
				loading: action.payload,
			};
		case UPDATE_DRIVER:
			return {
				...state,
				data: {...state.data, ...action.payload},
			};
		default:
			return state;
	}
}
