import {ThunkAction} from "redux-thunk";
import {ParsedQuery} from "query-string";
import {RootState} from "../";

export const SET_RESERVATION_LOADING = "SET_RESERVATION_LOADING";
export const SET_RESERVATIONS = "SET_RESERVATIONS";
export const UPDATE_FILTERS = "UPDATE_FILTERS";
export const SET_RESERVATION = "SET_RESERVATION";
export const UPDATE_RESERVATION = "UPDATE_RESERVATION";

export interface ReservationsState {
	filters: IFilters;
	loading: boolean;
	data: IReservationsData;
	reservation: IReservation | null;
}

interface SetLoadingAction {
	type: typeof SET_RESERVATION_LOADING;
	payload: boolean;
}

interface SetReservationsAction {
	type: typeof SET_RESERVATIONS;
	payload: IReservationsData;
}

interface UpdateFiltersAction {
	type: typeof UPDATE_FILTERS;
	payload: ParsedQuery;
}

interface SetReservationAction {
	type: typeof SET_RESERVATION;
	payload: IReservation;
}

interface UpdateReservationAction {
	type: typeof UPDATE_RESERVATION;
	payload: Partial<IReservation>;
}

export type ReservationsActionTypes =
	| SetLoadingAction
	| SetReservationsAction
	| UpdateFiltersAction
	| SetReservationAction
	| UpdateReservationAction;

export type ReservationsThunkAction = ThunkAction<
	Promise<void>,
	RootState,
	unknown,
	ReservationsActionTypes
>;
