import React from "react";
import {useTypedSelector} from "../../../redux/";

import styles from "./taxes.module.sass";

export const TaxCard: React.FC = () => {
	const reservation = useTypedSelector((store) => store.reservations.reservation);
	const {extras_data, prices, currency} = reservation as IReservation;
	const {vehicle, taxesTotal} = extras_data.taxes;

	return (
		<div className="card ai-end">
			<div className={`column column--last ${styles.column}`}>
				<div className={`column__row jc-sb ai-c ${styles.row}`}>
					<span>{vehicle.name}</span>
					<div className="price">
						{taxesTotal}&nbsp;{currency.symbol}
					</div>
				</div>
				<div className={`column__row jc-sb ai-c ${styles.row}`}>
					<span className={`label-title ${styles["label-title"]}`}>Subtotal:</span>
					<div className="price bold">
						{prices.total}&nbsp;{currency.symbol}
					</div>
				</div>
			</div>
		</div>
	);
};
