import React, {useState} from "react";
import {Select} from "../../";
// import {PaymentModal} from "./PaymentModal";

import styles from "./billing.module.sass";

interface IPayments {
	methods: paymentMethodType[];
}

export const Payments: React.FC<IPayments> = ({methods}) => {
	const [payment, setPayment] = useState<IOption | null>(null);

	const options = methods.map((method) => ({
		value: method.value,
		label: method.name,
		icon: method.image,
	}));

	const onPaymenthange = (selected: IOption) => {
		setPayment(selected);
	};

	return (
		<Select
			className={styles.payment}
			name="payments"
			onChange={onPaymenthange}
			options={options}
			placeholder="Payment method"
			value={payment}
			withIcon
		/>
	);
};
