import React from "react";
import classnames from "classnames";
import {DateLocation, Button, Map, RentalTime, Annual} from "../..";

import styles from "./vc.module.sass";

import {useMedia, useLocaionView} from "../../../hooks";

interface ILocationDateView {
	reservation: IReservation;
	changeLocation: () => void;
}

export const LocationDateView: React.FC<ILocationDateView> = ({reservation, changeLocation}) => {
	const media = useMedia(["(max-width: 575px)"], ["xs"], "l");
	//
	const {location_from, location_to, date_from, date_to} = reservation;
	const lowerCaseStatus = reservation.status.toLowerCase();
	const isChangeble =
		lowerCaseStatus !== "cancel" && lowerCaseStatus !== "reject" && lowerCaseStatus !== "done";
	//get map params
	const {mapCenter, showPickupLocation, showReturnLocation} = useLocaionView(reservation);

	const changeLocationHandler = (e: React.MouseEvent<HTMLButtonElement>) => {
		changeLocation();
	};

	const renderControls = (): React.ReactElement => (
		<div className={`card__actions ${styles.actions}`}>
			<Button text="Change" size="small" onClick={changeLocationHandler} />
			<Annual />
		</div>
	);

	return (
		<div className="column__row jc-fs ai-fs column__body">
			<div className={styles["dl-cotainer"]}>
				<div className={styles["dl-item"]}>
					<div className="label">Pick Up</div>
					<DateLocation
						date={date_from}
						location={
							location_from.type === "custom" ? location_from.address : location_from.office.title
						}
						onLocationClick={showPickupLocation}
					/>
				</div>
				<div className={styles["dl-item"]}>
					<div className="label">Return</div>
					<DateLocation
						date={date_to}
						location={
							location_to.type === "custom" ? location_to.address : location_to.office.title
						}
						onLocationClick={showReturnLocation}
					/>
				</div>
				{isChangeble && media === "l" && renderControls()}
			</div>
			<div className="column column--last">
				{isChangeble && <RentalTime date={date_from} />}
				<div
					className={classnames(styles["map-place"], {
						[styles["map-place--mt"]]: isChangeble,
					})}>
					<Map center={mapCenter} marker={true} />
				</div>
			</div>
			{isChangeble && media === "xs" && renderControls()}
		</div>
	);
};
