import React, {useState} from "react";
import {Button} from "../";

import styles from "./signature.module.sass";

import {useSignature} from "../../hooks";

interface ISignature {
	preview?: string;
	onConfirm?: (signature: string, file: Blob) => void;
}
export const Signature: React.FC<ISignature> = ({preview = "", onConfirm}) => {
	const [showPreview, setShowPreview] = useState(!!preview);

	const hidePreview = (): void => {
		setShowPreview(false);
	};

	const {signRef, clearSignature, confirmSignature} = useSignature({
		onDraw: hidePreview,
	});

	const clearHandler = (e: React.MouseEvent<HTMLButtonElement>) => {
		clearSignature();
		hidePreview();
	};

	const confirmHandler = (e: React.MouseEvent<HTMLButtonElement>) => {
		confirmSignature((signature, file) => {
			onConfirm && onConfirm(signature, file);
		});
	};

	return (
		<div className={styles.sign}>
			<div className={styles.sign__field}>
				{showPreview && <img className={styles.sign__preview} src={preview} alt="" />}
				<canvas ref={signRef} />
			</div>
			<div className={`${styles.sign__controls} rs-modal__controls`}>
				<Button text="Clear" onClick={clearHandler} />
				<Button text="Confirm" onClick={confirmHandler} />
			</div>
		</div>
	);
};
