import React from "react";
import Skeleton from "react-loading-skeleton";

import styles from "../ReservationsList/rslist.module.sass";

import dlStyles from "../DateLocation/dl.module.sass";

export const ReservationListSkeleton: React.FC = () => {
	return (
		<>
			<ReservationListItemSkeleton />
			<ReservationListItemSkeleton />
		</>
	);
};

const ReservationListItemSkeleton: React.FC = () => {
	return (
		<div className={styles.reservation}>
			<div className={styles.reservation__header}>
				<Skeleton width={160} height={14} style={{marginBottom: 5, marginRight: 15}} />
				<Skeleton width={285} height={14} style={{marginBottom: 5}} />
			</div>
			<div className={styles.reservation__body}>
				<div className={styles.side}>
					<div className={styles.vehicle}>
						<div className={styles.vehicle__info} style={{width: 150}}>
							<div className={styles.vehicle__img}>
								<img src="/img/img-place.svg" alt="" style={{objectFit: "unset"}} />
							</div>
							<Skeleton width={130} height={13} style={{marginTop: 10}} />
							<Skeleton width={150} height={23} style={{marginTop: 5}} />
							<Skeleton width={50} height={11} style={{marginTop: 2}} />
						</div>
						<div className={styles.vehicle__info} style={{width: 110}}>
							<Skeleton width={90} height={12} />
							<Skeleton width={90} height={12} count={3} style={{marginTop: 10, marginRight: 20}} />
						</div>
					</div>
				</div>
				<div className={styles.side}>
					<div className={styles.details}>
						<div className={`${styles.details__column} ${styles["details__column--mob"]}`}>
							<div className={styles["mob-flex"]}>
								<Skeleton width={120} height={14} style={{marginBottom: 10}} />
								<div className={dlStyles.container}>
									<Skeleton width={33} height={38} style={{marginRight: 7}} />
									<div>
										<Skeleton width={80} height={12} style={{display: "block"}} />
										<Skeleton width={80} height={12} style={{display: "block", marginTop: 5}} />
									</div>
								</div>
							</div>
							<div className={styles["mob-flex"]}>
								<Skeleton width={120} height={14} style={{marginBottom: 10}} />
								<div className={dlStyles.container}>
									<Skeleton width={33} height={38} style={{marginRight: 7}} />
									<div>
										<Skeleton width={80} height={12} style={{display: "block"}} />
										<Skeleton width={80} height={12} style={{display: "block", marginTop: 5}} />
									</div>
								</div>
							</div>
						</div>
						<div className={styles.details__column}>
							<Skeleton width={180} height={14} />
							<div>
								<Skeleton width={50} height={14} style={{marginTop: 8, marginBottom: 10}} />
							</div>
							<Skeleton width={180} height={14} count={2} />
							<div className={styles.details__group}>
								<div className={styles["details__group-item"]} style={{width: 70}}>
									<Skeleton width={60} height={14} style={{marginBottom: 10}} />
									<Skeleton width={70} height={18} />
								</div>
								<div className={styles["details__group-item"]} style={{width: 70}}>
									<Skeleton width={60} height={14} style={{marginBottom: 10}} />
									<Skeleton width={70} height={18} />
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div className={styles.reservation__footer}>
				<div className={styles["reservation__bottom-info"]}>
					<Skeleton
						width={200}
						height={14}
						style={{display: "block", marginTop: 10, marginRight: 15}}
					/>
					<Skeleton
						width={270}
						height={14}
						style={{display: "block", marginTop: 10, marginRight: 15}}
					/>
				</div>
				<div className={styles.reservation__btns}>
					<Skeleton width={130} height={37} />
					<Skeleton width={130} height={37} />
				</div>
			</div>
		</div>
	);
};
