import React from "react";
import classnames from "classnames";
import Skeleton from "react-loading-skeleton";
import {useMedia} from "../../hooks/";

import styles from "../Reservation/VehicleCard/vc.module.sass";

import dlStyles from "../DateLocation/dl.module.sass";

export const VehicleCardSkeleton: React.FC = () => {
	const media = useMedia(["(max-width: 575px)", "(max-width: 767px)"], ["xs", "s"], "l");

	const renderReserveNum = (): React.ReactElement => (
		<div className={`${styles["reserve-info"]} mob-full`}>
			<div className="label" style={{verticalAlign: "bottom", marginRight: "10px"}}>
				<Skeleton width={70} height={13} />
			</div>
			<Skeleton width={80} height={24} />
		</div>
	);

	const renderControls = (): React.ReactElement => (
		<div className={`card__actions ${styles.actions}`}>
			<Skeleton width={72} height={20} />
			<Skeleton width={72} height={20} />
		</div>
	);

	return (
		<div className={`card ${styles.vc}`}>
			{media !== "l" && renderReserveNum()}
			<div className={`column ${styles.vehicle}`}>
				<div className={styles.vehicle__info}>
					<div className={styles.vehicle__img}>
						<img src="/img/img-place.svg" alt="" style={{objectFit: "unset"}} />
					</div>
					<div className={styles.vehicle__group}>
						<Skeleton width={180} height={14} />
					</div>
					<span className={styles.vehicle__title}>
						<Skeleton width={180} height={46} />
					</span>
				</div>
				<div className={styles.vehicle__info}>
					<Skeleton width={180} height={12} style={{display: "block", marginTop: 5}} />
					<Skeleton width={100} height={12} count={2} style={{display: "block", marginTop: 10}} />
				</div>
			</div>
			<div className="column column--2">
				<div className="column__row jc-fs ai-fs column__head">
					{media === "l" && renderReserveNum()}
					<div className={styles["reserve-info"]}>
						<div className="label">
							<Skeleton width={70} height={13} />
						</div>
						<Skeleton width={100} height={24} />
					</div>
					<div
						className={classnames(styles["reserve-info"], {
							[styles["reserve-info--status"]]: media === "l",
						})}>
						<div className="label">
							<Skeleton width={70} height={13} />
						</div>
						<Skeleton width={100} height={24} />
					</div>
				</div>
				<div className="column__row jc-fs ai-fs column__body">
					<div className={styles["dl-cotainer"]}>
						<div className={styles["dl-item"]}>
							<Skeleton width={120} height={13} style={{marginBottom: 10}} />
							<div className={dlStyles.container}>
								<Skeleton width={33} height={38} style={{marginRight: 7}} />
								<div>
									<Skeleton width={80} height={11} style={{display: "block"}} />
									<Skeleton width={80} height={12} style={{display: "block", marginTop: 5}} />
								</div>
							</div>
						</div>
						<div className={styles["dl-item"]}>
							<Skeleton width={120} height={13} style={{marginBottom: 10}} />
							<div className={dlStyles.container}>
								<Skeleton width={33} height={38} style={{marginRight: 7}} />
								<div>
									<Skeleton width={80} height={11} style={{display: "block"}} />
									<Skeleton width={80} height={12} style={{display: "block", marginTop: 5}} />
								</div>
							</div>
						</div>
						{media !== "xs" && renderControls()}
					</div>
					<div className="column column--last">
						<Skeleton width={180} height={13} style={{marginBottom: 10}} />
						<Skeleton width={180} height={18} />
						<Skeleton width={220} height={124} style={{marginTop: 15}} />
					</div>
					{media === "xs" && renderControls()}
				</div>
			</div>
		</div>
	);
};
