import React from "react";
import {useTypedSelector} from "../../../redux";
import {Button} from "../../";
import {Insurance} from "./Insurance";
import {Extra} from "./Extra";

import styles from "./extras.module.sass";

import {useCard, useExtras} from "../../../hooks";

export const ExtrasCard: React.FC = () => {
	const {open, toggleCard} = useCard();
	const reservation = useTypedSelector((store) => store.reservations.reservation) as IReservation;
	const {insurances, extras, currency, changeInsurance, changeExtras} = useExtras(
		reservation,
		open,
	);

	const lowerCaseStatus = reservation.status.toLowerCase();
	const isChangeble =
		lowerCaseStatus !== "cancel" && lowerCaseStatus !== "reject" && lowerCaseStatus !== "done";

	const changeBtnClick = (e: React.MouseEvent<HTMLButtonElement>): void => {
		toggleCard();
	};

	return (
		<div className={`card ${styles.card}`}>
			<div className="column column--title">
				<div className="column__row ai-c">
					<span className="card__title">Extras</span>
					{isChangeble && !open && <Button text={"Change"} size="small" onClick={changeBtnClick} />}
				</div>
			</div>
			<div className="column column--2">
				<div className="label-title">Insurance</div>
				<div>
					{insurances.map((insurance) => (
						<Insurance
							key={insurance.id}
							editable={open}
							{...insurance}
							currency={currency.symbol}
							onChange={changeInsurance}
						/>
					))}
				</div>
				{extras.length > 0 && (
					<>
						<div className="label-title">Extras</div>
						<div>
							{extras.map((extra) => (
								<Extra
									key={extra.id}
									editable={open}
									{...extra}
									currency={currency.symbol}
									onChange={changeExtras}
								/>
							))}
						</div>
					</>
				)}
			</div>
		</div>
	);
};
