import React, {useState} from "react";
import {useDispatch} from "react-redux";
import Modal from "react-modal";
import {rejectReservation} from "../../../redux/actions";
import {Button} from "../../";

import styles from "./annul.module.sass";

import {useModal} from "../../../hooks";

export const Annual: React.FC = () => {
	const dispatch = useDispatch();
	const [modalOpen, toggleModal] = useModal();
	const [message, setMessage] = useState("");
	const [error, setError] = useState("");

	const openAnnualModalHandler = (e: React.MouseEvent<HTMLButtonElement>): void => {
		toggleModal(true);
	};

	const closeModalHandler = (e: React.MouseEvent<HTMLButtonElement>): void => {
		toggleModal(false);
	};

	const onAnnualConfirm = (e: React.MouseEvent<HTMLButtonElement>): void => {
		if (message.length < 20) {
			setError("Needed more than 20 symbols");
		} else {
			setError("");
			dispatch(
				rejectReservation(
					message,
					//TODO: process erros for annual reservation
				),
			);
			toggleModal(false);
		}
	};

	const messageChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
		setMessage(e.target.value);
	};

	return (
		<>
			<Button text="Annual" size="small" onClick={openAnnualModalHandler} />
			<Modal
				className={`rs-modal ${styles.annual__modal}`}
				isOpen={modalOpen}
				contentLabel="Sign Modal"
				onRequestClose={closeModalHandler}>
				<div className="rs-modal__header">
					<span className="rs-modal__title">Why Annul?</span>
					<button className="rs-modal__close" onClick={closeModalHandler}>
						<span>×</span>
					</button>
				</div>
				<div className="rs-modal__body">
					<textarea className={styles.message} value={message} onChange={messageChange} rows={5} />
					{error && <p className={styles.annual__error}>{error}</p>}
				</div>
				<div className="rs-modal__footer rs-modal__controls">
					<Button text="Cancel" onClick={closeModalHandler} />
					<Button text="Annual" onClick={onAnnualConfirm} />
				</div>
			</Modal>
		</>
	);
};
